import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './task-page.css';
import TopMenu from '../../top-menu';

import MobileMenuTask from '../../mobile-menu-task';
import TaskService from '../../../services/task-service';
import PopUpContext from '../../context/popup-context';


// import { useParams } from "react-router-dom";


export default class TaskPage extends Component {

    state = {
        TaskService: new TaskService(),
        TaskInfo: null,
        GroupLink: null,
        UserRole: null,
        UserHomework: null,
        TaskFiles: null,
        MyFiles: null,
        AllHomeWorks: [],
    }


    componentDidMount() {
        const taskLink = this.props.match.params.link
        this.state.TaskService.getSingleTask(taskLink).then(
            res => {
                console.log(res);
                if (res["code"] === undefined) {
                    this.state.TaskService.getTaskAdminRole(taskLink).then(
                        resRole => {
                            console.log(resRole);
                            this.state.TaskService.getTaskFiles(res["response"][0]["group_link"], taskLink).then(
                                resFiles => {
                                    console.log(resFiles);
                                    this.state.TaskService.getSingleHomework(taskLink).then(
                                        homeworkRes => {
                                            console.log(homeworkRes);
                                            this.state.TaskService.getMyFiles(res["response"][0]["group_link"], taskLink).then(
                                                myFilesRes => {
                                                    console.log(myFilesRes);
                                                    this.setState({
                                                        UserHomework: homeworkRes["response"][0],
                                                        UserRole: resRole["response"],
                                                        TaskInfo: res["response"][0],
                                                        TaskFiles: resFiles["response"],
                                                        MyFiles: myFilesRes["response"],
                                                        GroupLink: res["response"][0]["group_link"],
                                                    });
                                                });
                                        });
                                });
                        });
                } else {
                    window.location = "/";
                }
            });
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.state.UserRole === "admin" || this.state.UserRole === "teacher") {
            if (prevState.AllHomeWorks === this.state.AllHomeWorks) {
                this.state.TaskService.getAllHomework(this.state.TaskInfo.task_link, this.state.TaskInfo.group_link).then(
                    res => {
                        console.log(res);
                        this.setState({
                            AllHomeWorks: res["response"]
                        });
                    }
                );
            }
        }
    }


    onNewHomework = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        console.log(formData);

        this.state.TaskService.newHomework(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location.reload();
                } else {
                    alert("Error! Check console");
                }
            }
        );
    }

    deleteTaskFileAdmin = (fileId) => {
        this.state.TaskService.deleteTaskFileAdmin(fileId, this.state.TaskInfo.group_link).then(
            res => {
                console.log(res);
                window.location.reload();
            }
        );
    }


    deleteMyTaskFile = (fileId) => {
        this.state.TaskService.deleteMyTaskFile(fileId, this.state.TaskInfo.group_link).then(
            res => {
                console.log(res);
                window.location.reload();
            }
        );
    }

    render() {

        const { TaskInfo, UserRole, UserHomework, AllHomeWorks, TaskFiles, MyFiles, GroupLink } = this.state;

        // console.log(AllHomeWorks);
        console.log(MyFiles)

        const { handlePopUpContent, reformatDate, serverUrl } = this.context;

        let userData = localStorage.getItem("user");
        userData = JSON.parse(userData);
        // console.log(userData);
        const userIcon = serverUrl + userData["response"][0]["picture"];
        const userName = userData["response"][0]["name"];


        const userElements = {
            display: UserRole === "student" ? 'block' : 'none',
        }

        const adminElements = {
            display: UserRole === "admin" || UserRole === "teacher" ? 'block' : 'none',
        }
            ;

        if (TaskInfo !== null) {

            const TaskNewFiles = () => {
                return (
                    <div className="files-block">
                        <h4>Attached files:</h4>
                        <div style={adminElements}>
                            <div className="files-action" onClick={() => handlePopUpContent("newTaskFile", { taskLink: TaskInfo.task_link, currentGroupLink: TaskInfo.group_link })}>
                                <img src={"/assets/svg/pin.svg"} alt="Attach" />
                                Attach new file
                        </div>
                        </div>
                    </div>
                )
            }

            const TaskFilesBlock = () => TaskFiles.map((file) =>
                <div key={file.id} className="file-line">
                    <span className="name">{file.file_name}</span>
                    <div className="right-side">
                        <span className="type">{file.type}</span>
                        <a target='_blank' rel="noopener noreferrer" href={file.file_link}><img src="/assets/svg/download.svg" alt="Open" /></a>
                        <img style={adminElements} onClick={() => this.deleteTaskFileAdmin(file.id)} className="delete" src={"/assets/svg/x.svg"} alt="Delete" />
                    </div>
                </div>
            );

            const MyTaskNewFiles = () => {
                return (
                    <div className="files-block user" style={userElements}>
                        <h4>Attached files:</h4>
                        <div className="files-action" onClick={() => handlePopUpContent("newTaskFile", { taskLink: TaskInfo.task_link, currentGroupLink: TaskInfo.group_link })}>
                            <img src={"/assets/svg/pin.svg"} alt="Attach" />
                            Attach new file
                        </div>
                    </div>
                )
            }

            const MyTasksFilesBlock = () => MyFiles.map((file) =>
                <div key={file.id} className="file-line">
                    <span className="name">{file.file_name}</span>
                    <div className="right-side">
                        <span className="type">{file.type}</span>
                        <a target='_blank' rel="noopener noreferrer" href={file.file_link}><img src="/assets/svg/download.svg" alt="Open" /></a>
                        <img onClick={() => this.deleteMyTaskFile(file.id)} className="delete" src={"/assets/svg/x.svg"} alt="Delete" />
                    </div>
                </div>
            );

            let StatusBlock;

            if (UserHomework) {
                switch (UserHomework.status) {
                    case "open":
                        StatusBlock = () => <div className="task_status blue">Sent</div>
                        break;
                    case "checked":
                        StatusBlock = () => <div className="task_status green">Done</div>
                        break;
                }
            } else {
                StatusBlock = () => <div className="task_status red">To be done</div>
            }


            const AllHomeWorksBlock = () => AllHomeWorks.map((hwork) =>
                <Link to={"/admin/homework/" + hwork.homework_link} key={hwork.homework_link} className="link-wrapper">
                    <div key={hwork.homework_link} className="homework-link">
                        <div className="user-part">
                            <img src={serverUrl + hwork.user.picture} alt="User Pic" />
                            <span>{hwork.user.name}</span>
                        </div>
                        <div className="info-action-part">
                            {hwork.status === "open" ? <span className="green">Not checked</span> : null}
                            {hwork.status === "checked" ? <span>{hwork.teacher_mark}/{TaskInfo.max_mark}</span> : null}
                            <Link to={"/admin/homework/" + hwork.homework_link} className="action">Open</Link>
                        </div>
                    </div>
                </Link>
            )

            return (
                <React.Fragment>
                   {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} /> }

                    <div className="goback_button" onClick={() => window.history.back()}>
                        <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>

                    <div className="taskpage action-space">
                        <div className="page_heading" id="task-scroll">Task {UserRole === "admin" || UserRole === "teacher" ? <img onClick={() => handlePopUpContent("editTask", TaskInfo.task_link)} className="edit_task_icon" src="/assets/svg/edit.svg" alt="Edit Task" /> : null}</div>
            {UserRole === "admin" || UserRole === "teacher" ? <div className="task_status blue">The work has been sent by {AllHomeWorks.length} student(s)</div> : <StatusBlock />}
                        <div className="wrapper">
                            <div className="side-l">
                                <div className="task_heading">
                                    <div className={"task_circle small_circle " + TaskInfo.color}></div>
                                    {TaskInfo.name}
                                </div>
                                <div className="task_deadline">
                                    Untill {reformatDate(TaskInfo.deadline.substr(0, 10))}
                                </div>
                                <div className="task_description">
                                    {TaskInfo.comment}
                                </div>
                                <div style={userElements}>
                                    <TaskNewFiles />
                                    {TaskFiles ? <div className="files-list"><TaskFilesBlock /></div> : <p className="files-message">Teacher hasn't attached any files yet...</p>}
                                </div>
                            </div>

                            <div className="side-r" style={userElements}>
                                {UserHomework ? (
                                    <React.Fragment>
                                        <div className="task_heading" id="yourwork-scroll">Your work</div>
                                        {MyFiles ? (
                                            <div className="task_deadline">Mark {UserHomework.teacher_mark}/{TaskInfo.max_mark}</div>
                                        ) : (
                                                <div className="homework-info-line">
                                                    <div className="blue">Step 2/2</div>
                                                    <div className="gray">Mark 0/{TaskInfo.max_mark}</div>
                                                </div>
                                            )}
                                        {/* <div className="task_deadline">Mark {UserHomework.teacher_mark}/{TaskInfo.max_mark}</div> */}
                                        <div className="homework-post">
                                            <img src={userIcon} alt="User Pic" />
                                            <div className="user-text">
                                                <span>{userName}</span>
                                                <p>{UserHomework.comment}</p>
                                            </div>
                                        </div>

                                        {UserRole === "student" && UserHomework.teacher_message ? (
                                            <div className="homework-post">
                                                <img src={serverUrl + UserHomework.teachers.teacherMessageSender.picture} />
                                                <div className="user-text">
                                                    <span>{UserHomework.teachers.teacherMessageSender.name}</span>
                                                    <p>{UserHomework.teacher_message}</p>
                                                </div>
                                            </div>
                                        ) : null}

                                        {UserRole === "student" && UserHomework.teacher_comment ? (
                                            <div className="homework-post">
                                                <img src={serverUrl + UserHomework.teachers.teacherCommentSender.picture} />
                                                <div className="user-text">
                                                    <span>{UserHomework.teachers.teacherCommentSender.name}</span>
                                                    <p>{UserHomework.teacher_comment}</p>
                                                </div>
                                            </div>
                                        ) : null}
                                        {/* <div className="step">Step 2/2</div> */}
                                        <MyTaskNewFiles />
                                        {MyFiles ? <div className="files-list"><MyTasksFilesBlock /></div> : <div className="step margin">Attach files to continue...</div>}
                                        <br /><br />
                                        {MyFiles ? <button className="main gray send-work">Already Sent</button> : <button className="main gray send-work">Needs Attachment</button>}
                                    </React.Fragment>
                                ) : (
                                        <form onSubmit={this.onNewHomework}>
                                            <div className="task_heading">Send your work</div>
                                            <div className="homework-info-line">
                                                <div className="blue">Step 1/2</div>
                                                <div className="gray">Mark 0/{TaskInfo.max_mark}</div>
                                            </div>
                                            {/* <div className="task_deadline">Mark 0/{TaskInfo.max_mark}</div>
                                            <div className="step">Step 1/2</div> */}
                                            <textarea name="comment" className="work" placeholder="Attach a comment to your work..."></textarea>
                                            <input type="hidden" name="taskLink" value={TaskInfo.task_link} />
                                            <br /><br />
                                            <button type="submit" className="main send-work">Next</button>
                                        </form>
                                    )}
                            </div>

                            <div className="side-r" style={adminElements}>
                                <TaskNewFiles />
                                {TaskFiles ? <div className="files-list"><TaskFilesBlock /></div> : <p className="files-message-admin">You haven't attached any files yet...</p>}
                            </div>
                        </div>

                        <div className="homeworks-all"  id="studentswork-scroll" style={adminElements}>
                            <div className="blue-heading" >Students homework</div>
                            {AllHomeWorks ? <AllHomeWorksBlock /> : "No homeworks yet..."}
                        </div>
                    </div>
                   {UserRole === "admin" || UserRole === "teacher" ? <MobileMenuTask isTeacher="true" /> : <MobileMenuTask />} 
                </React.Fragment>
            );
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

TaskPage.contextType = PopUpContext;