import React, { Component } from 'react';

import TaskService from '../../../services/task-service';

import PopUpContext from '../../context/popup-context';


import './admin.css';

// import { useParams } from "react-router-dom";


export default class HomeWorkPage extends Component {
    state = {
        TaskService: new TaskService(),
        TaskInfo: null,
        UserHomework: null,
        HomeworkFiles: null,
    }


    componentDidMount() {
        const homeworkLink = this.props.match.params.link
        this.state.TaskService.getSingleHomeworkAdmin(homeworkLink).then(
            res => {
                console.log(res);
                if (res["response"] !== undefined) {
                    this.state.TaskService.getSingleTask(res["response"][0]["task_link"]).then(
                        taskRes => {
                            console.log(taskRes);
                            this.state.TaskService.getStudentTaskFiles(res["response"][0]["group_link"], homeworkLink).then(
                                homeworkFilesRes => {
                                    console.log(homeworkFilesRes);
                                    this.setState({
                                        TaskInfo: taskRes["response"][0],
                                        UserHomework: res["response"][0],
                                        HomeworkFiles: homeworkFilesRes["response"],
                                    });
                                }
                            )
                        }
                    )
                } else {
                    window.location = "/";
                }
            });
    }


    onNewHomework = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        console.log(formData);

        this.state.TaskService.newHomework(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location.reload();
                } else {
                    alert("Error! Check console");
                }
            }
        );
    }

    attachTeacherMessage = () => {
        let comment = document.getElementById("teacherComment").value;

        this.state.TaskService.commentHomeworkAdmin(this.state.UserHomework.homework_link, comment).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location.reload();
                } else {
                    alert("Error! Check console");
                }
            }
        )
    }

    render() {

        const { TaskInfo, UserHomework, HomeworkFiles } = this.state;

        const { handlePopUpContent, serverUrl } = this.context;

        if (TaskInfo !== null) {

            const senderIcon = serverUrl + UserHomework.user.picture;
            const senderName = UserHomework.user.name;

            const TaskFiles = () => {
                return (
                    <h4>Attached files:</h4>
                )
            }

            const StudentTaskFiles = () => {
                return (
                    <h4 className="student-files-heading">Student files:</h4>
                )
            }

            const HomeworkFilesBlock = () => HomeworkFiles.map((file) =>
                <div key={file.id} className="file-line">
                    <span className="name">{file.file_name}</span>
                    <div className="right-side">
                        <span className="type">{file.type}</span>
                        <a target='_blank' rel="noopener noreferrer" href={file.file_link}><img src="/assets/svg/download.svg" alt="Open" /></a>
                    </div>
                </div>
            );

            return (
                <div className="taskpage homework">
                    <div className="page_heading">Work</div>
                    <div className="header-user-line">
                        <img src={senderIcon} alt="User Pic" />
                        <span>{senderName}</span>
                    </div>
                    {/* {UserHomework ? <div className="task_status blue">Sent</div> : <div className="task_status red">To be done</div>} */}
                    <div className="wrapper">
                        <div className="side-l">
                            <div className="task_heading">
                                <div className={"task_circle small_circle " + TaskInfo.color}></div>
                                {TaskInfo.name}
                            </div>
                            <div className="task_deadline">
                                Untill {TaskInfo.deadline.substr(0, 10)}
                            </div>
                            <div className="task_description">
                                {TaskInfo.comment}
                            </div>
                            <TaskFiles />
                        </div>

                        <div className="side-r">
                            <div className="task_heading">Student work</div>
                            <div className="task_deadline second">Sent: {UserHomework.created_date.substr(0, 10)}</div>
                            <div className="homework-post">
                                <img src={senderIcon} alt="User Pic" />
                                <div className="user-text">
                                    <span>{senderName}</span>
                                    <p>{UserHomework.comment}</p>
                                </div>
                            </div>

                            {UserHomework.teacher_message ? (
                                <div className="homework-post">
                                    <img src={serverUrl + UserHomework.teachers.teacherMessageSender.picture} />
                                    <div className="user-text">
                                        <span>{UserHomework.teachers.teacherMessageSender.name}</span>
                                        <p>{UserHomework.teacher_message}</p>
                                    </div>
                                </div>
                            ) : null}

                            {UserHomework.teacher_comment ? (
                                <div className="homework-post">
                                    <img src={serverUrl + UserHomework.teachers.teacherCommentSender.picture} />
                                    <div className="user-text">
                                        <span>{UserHomework.teachers.teacherCommentSender.name}</span>
                                        <p>{UserHomework.teacher_comment}</p>
                                    </div>
                                </div>
                            ) : null}

                            <div className="stroke"></div>
                            {UserHomework.teacher_message === null ? (
                                <div className="teacher-comment">
                                    <textarea id="teacherComment" name="comment" placeholder="Attach a comment..."></textarea>
                                    <img onClick={this.attachTeacherMessage} className="teacher-comment-button" src="/assets/svg/send.svg" />
                                </div>
                            ) : null}
                            <StudentTaskFiles />
                            {HomeworkFiles ? <div className="files-list"><HomeworkFilesBlock /></div> : <p className="files-message">Student hasn't attached any files yet...</p>}
                            <br /><br />
                            {UserHomework.teacher_comment ? (
                                <button className="main gray send-work">Already Rated</button>
                            ) : (
                                    <button onClick={() => handlePopUpContent("rateHomework", { task_link: TaskInfo.task_link, homework_link: UserHomework.homework_link, max_mark: TaskInfo.max_mark })} className="main send-work">Rate the work</button>
                                )}
                        </div>
                    </div>
                </div>
            );
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

HomeWorkPage.contextType = PopUpContext;