import React, { Component } from "react";

import GroupService from '../../../services/group-service';

export default class NewLesson extends Component {

    state = {
        GroupService: new GroupService(),
        checkboxIsChecked: false
    }

    componentDidMount() {
        this.firstInput.focus();
    }

    handleCheckboxChange = (e) => {
        let checked = e.target.checked;
        console.log(checked)
        this.setState({
            checkboxIsChecked: checked
        })
      }

    onEventCreate = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        console.log(formData)

        this.state.GroupService.newLesson(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    // alert("Task created!")
                    window.location = "/admin/manage-lessons/" + res["response"]["link"];
                } else {
                    alert(res["response"]["message"]);
                }
            }
        );
    }

    render() {
        let today = new Date();
        today = today.toISOString().substring(0, 10);

        return (
            <div className="popup_form">
                <h4>Add a Lesson (Event)</h4>
                <form onSubmit={this.onEventCreate}>
                    <div className="form_elements">
                        <div className="form_input_section">
                            <input type="text" ref={(input) => { this.firstInput = input; }} name="title" placeholder="Event name (ex: Math Class)" required />
                            <span className="label">Lesson name</span>
                        </div>
                        <div className="form_input_section">
                            <input type="text" name="description" placeholder="(Event description)" required />
                            <span className="label">Description</span>
                        </div>
                        <div className="form_input_section">
                            <input type="date" min={today} name="eventDate" placeholder="Select the event date" required />
                            <span className="label">Date</span>
                        </div>
                        <div className="checkbox-line">
                        <input type="checkbox" onChange={ this.handleCheckboxChange } value="true" /> <span className="checkbox-info">Repeat every week</span>
                        </div>
                        <br/><br/>
                        {/* <div className="form_input_section">
                            <select name="daysOfWeek">
                                <option value="0">Sunday</option>
                                <option value="1">Monday</option>
                                <option value="2">Tuesday</option>
                                <option value="3">Wednesday</option>
                                <option value="4">Thursday</option>
                                <option value="5">Friday</option>
                                <option value="6">Saturday</option>
                            </select>
                            <span className="label">Week Day</span>
                        </div> */}
                        <div className="form_input_section">
                            <input type="time" placeholder="Select the event time" name="startTime" min="00:00" max="24:00" required />
                            <span className="label">Time</span>
                        </div>
                        <div className="form_input_section">
                            <input type="text" name="lessonUrl" placeholder="(Google Meet, Zoom...)" />
                            <span className="label">Lesson URL (Link)</span>
                        </div>
                        <input type="hidden" name="link" value={this.props.currentGroupLink} />
                        <input type="hidden" name="recurring" value={this.state.checkboxIsChecked} />
                        <br/><br/><br/><br/>
                        <div className="bottom-gradient"></div>
                    </div>
                    <div className="form_action">
                        <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                        <button className="main" type="submit">Create</button>
                    </div>
                </form>
            </div>
        );
    }
}