export default class UserService {

    // _apiBase = 'http://localhost:5000/api';
    _apiBase = 'https://api.jetdesk.app/api';

    xhr = new XMLHttpRequest();

    getResource = async (url) => {
        const res = await fetch(`${this._apiBase}${url}`);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}` +
                `, received ${res.status}`)
        }
        return await res.json();
    };

    // getAllPeople = async () => {
    //     const res = await this.getResource(`/people/`);
    //     return res.results
    //         .map(this._transformPerson)
    //         .slice(0, 5);
    // };


    uploadFile = (token, formEl) => {
        const { xhr } = this;
        let url = this._apiBase + "/user/upload";
        xhr.open("POST", url);
        xhr.setRequestHeader('Authorization', 'Bearer ' + token);
        // xhr.setRequestHeader("Content-Type","multipart/form-data");

        let formData = new FormData(formEl);
        xhr.send(formData);

        return new Promise((resolve, reject) => {
            xhr.onreadystatechange = (e) => {
                if (xhr.readyState === 4) {
                    console.log(xhr.responseText);
                    resolve(JSON.parse(xhr.responseText));
                }
            }

            xhr.onerror = (e) => {
                // alert(xhr.responseText); 
                alert("error");
            }
        });
    }

    getUserData = (token) => {
        const { xhr } = this;
        let url = this._apiBase + "/user/data";
        xhr.open("POST", url);
        xhr.setRequestHeader('Authorization', 'Bearer ' + token);
        xhr.send();
        return new Promise((resolve, reject) => {
            xhr.onreadystatechange = (e) => {
                if (xhr.readyState === 4) {
                    console.log(xhr.responseText);
                    resolve(JSON.parse(xhr.responseText));
                }
            }
        });
    }

    getUserLimit = (token) => {
        const { xhr } = this;
        let url = this._apiBase + "/user/limit";
        xhr.open("POST", url);
        xhr.setRequestHeader('Authorization', 'Bearer ' + token);
        xhr.send();
        return new Promise((resolve, reject) => {
            xhr.onreadystatechange = (e) => {
                if (xhr.readyState === 4) {
                    console.log(xhr.responseText);
                    resolve(JSON.parse(xhr.responseText));
                }
            }
        });
    }

    editUserProfile = (opts) => {
        const { xhr } = this;
        let url = this._apiBase + "/user/edit";
        xhr.open("POST", url);
        // xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.setRequestHeader('Authorization', 'Bearer ' + localStorage.getItem('token'));
        const postData = { ...opts };
        xhr.send(JSON.stringify(postData));
        return new Promise((resolve, reject) => {
            xhr.onreadystatechange = (e) => {
                if (xhr.readyState === 4) {
                    console.log(xhr.responseText);
                    resolve(JSON.parse(xhr.responseText));
                }
            }
        });
    }

    changeUserPassword = (opts) => {
        const { xhr } = this;
        let url = this._apiBase + "/user/password";
        xhr.open("POST", url);
        // xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.setRequestHeader('Authorization', 'Bearer ' + localStorage.getItem('token'));
        const postData = { ...opts };
        xhr.send(JSON.stringify(postData));
        return new Promise((resolve, reject) => {
            xhr.onreadystatechange = (e) => {
                if (xhr.readyState === 4) {
                    console.log(xhr.responseText);
                    resolve(JSON.parse(xhr.responseText));
                }
            }
        });
    }

  

}
