import React, { Component } from "react";

import GroupService from '../../../services/group-service';

export default class AddGroupPopupContent extends Component {

    state = {
        GroupService: new GroupService(),
    }

    componentDidMount() {
        this.firstInput.focus();
    }


    onGroupCreate = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.GroupService.newGroup(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location = "/class/" + res["response"]["link"];
                } else {
                    alert(res["response"]["message"]);
                }
            }
        );
    }

    render() {
        return (
            <div className="popup_form">
                <h4>Create a class</h4>
                <form onSubmit={this.onGroupCreate}>
                    <div className="form_elements">
                        <div className="form_input_section">
                            <input type="text" ref={(input) => { this.firstInput = input; }} name="name" placeholder="Group name (ex.: Math class)" autoComplete="off" required />
                            <span className="label">Group name</span>
                        </div>
                        <div className="form_input_section">
                            <input type="text" name="schoolName" placeholder="(Could be your or a personal brand)" required />
                            <span className="label">School name</span>
                        </div>
                        <div className="form_input_section">
                            <input type="text" name="code" placeholder="Ex.: MySecretPass123" autoComplete="off" />
                            <span className="label">(Leave empty if you'd like your group to be open to anyone).</span>
                            <span className="label">Group invitation pass</span>
                        </div>
                    </div>
                    <div className="form_action">
                        <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                        <button className="main" type="submit">Create</button>
                    </div>
                </form>
            </div>
        );
    }
}