import React, { Component } from "react";

import UserService from '../../../services/user-service';

export default class UserPicture extends Component {

    state = {
        UserService: new UserService(),
    }

    onPictureUpload = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        this.state.UserService.uploadFile(localStorage.getItem("token"), document.getElementById('profile-picture-form')).then(
            res => {
                console.log(res);
                this.state.UserService.getUserData(localStorage.getItem("token")).then(
                    userData => {
                        console.log(userData)
                        localStorage.setItem('user', JSON.stringify(userData));
                        window.location.reload();
                    });
            }
        );
    }

    render() {
        return (
            <form id="profile-picture-form" encType="multipart/form-data" onSubmit={this.onPictureUpload}>
                <div className="form_content upload">
                    <div className="form_elements upload">
                        <img src="/assets/illustration/selfie.svg" className="popup-illustration" alt="selfie" />
                        <input type="file" id="profile-picture" accept="image/*" name="profilepic" />
                    </div>
                    <button className="main" type="submit"> Upload </button>
                    <div className="form_action upload">
                            <div className="cancel mobile-only" onClick={this.props.changePopUpState}>Cancel</div>
                    </div>
                </div>
            </form>
        );
    }
}