import React, { Component } from 'react';
// import { Link } from 'react-router-dom';

import GroupService from '../../../../services/group-service';

import PopUpContext from '../../../context/popup-context';

// import { useParams } from "react-router-dom";


export default class FilesBlock extends Component {

    state = {
        GroupService: new GroupService(),
        FilesList: null,
        GroupLink: null
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        const link = this.props.link;
        if (link !== this.state.GroupLink) {
            this.setState({
                GroupLink: link
            });
            this.state.GroupService.getGroupFiles(this.props.link).then(
                res => {
                    this.setState({
                        FilesList: res["response"]
                    });
                });
        }
    }



    render() {

        const { FilesList } = this.state;

        if (FilesList !== null) {

            if (FilesList !== false) {

                const FilesListBlock = () => FilesList.map((fileInfo, index) =>
                    <div className="file-line" key={fileInfo.file_name + index}>
                        <div className="top-line">
                            <span className="name">{fileInfo.file_name}</span>
                            <span className="type">{fileInfo.type}</span>
                            <a target='_blank' rel="noopener noreferrer" href={fileInfo.file_link}><img src="/assets/svg/download.svg" alt="Open" /></a>
                        </div>
                        <span className="comment">{fileInfo.file_comment.substr(0, 40)}</span>
                    </div>
                );

                return (
                    <div className="files-list">
                        <FilesListBlock />
                    </div>
                );
            } else {
                return (
                    <div className="widget_info">
                        <div className="info-message">
                            <img className="info-illustration" src="/assets/illustration/files-gray.svg" alt="Files" />
                            <span>Oops, a teacher hasn't attached any<br/> files yet</span>
                        </div>
                    </div>
                );
            }
        } else {
            return (
                <p>Loading...</p>
            );
        }
    }
}

FilesBlock.contextType = PopUpContext;