import React, { Component } from 'react';

import './admin.css';
import TopMenu from '../../top-menu';

import GroupService from '../../../services/group-service';
import PopUpContext from '../../context/popup-context';


// import { useParams } from "react-router-dom";


export default class GroupSettingsPage extends Component {

    state = {
        GroupService: new GroupService(),
        GroupLink: null,
        GroupInfo: null,
        UserRole: null,
    }

    componentDidMount() {
        this.componentDidUpdate()
    }

    componentDidUpdate() {
        let link = this.props.match.params.link;
        if (this.state.GroupLink !== link) {
            console.log("Update")
            this.setState({
                GroupLink: link
            });
            this.state.GroupService.getGroupDataAdmin(link).then(
                res => {
                    console.log(res);
                    if (res["response"] !== false) {
                        this.state.GroupService.getRole(link).then(
                            roleRes => {
                                // console.log(roleRes);
                                if (roleRes["response"] === "admin") {
                                    this.setState({
                                        UserRole: roleRes["response"]
                                    });
                                    this.setState({
                                        GroupInfo: res["response"][0]
                                    });
                                } else {
                                    window.location = "/";
                                }
                            }
                        );
                        // this.setState({
                        //     GroupInfo: res["response"][0]
                        // });
                    } else {
                        console.log("Not found");
                        window.location = "/";
                    }
                }
            );
        }
    }

    onGroupEdit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.GroupService.editGroup(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    // console.log("/class/" + this.state.GroupLink)
                    window.location = "/admin/settings/" + this.state.GroupLink;
                } else {
                    alert(res);
                }
            }
        );
    }

    copyToClipboard = (e) => {
        this.groupLinkField.select();
        document.execCommand('copy');
        // This is just personal preference.
        // I prefer to not show the whole text area selected.
        e.target.focus();
        alert('Copied!');
      };


    render() {
        const { GroupInfo, UserRole, GroupLink } = this.state;
        const { handlePopUpContent, serverUrl } = this.context;

        if (GroupInfo !== null) {
            return (
                <React.Fragment>
                    {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} /> }

                    <div className="goback_button" onClick={() => window.history.back()}>
                        <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>
                    <div className="group_settings_page action-space">
                        <div className="page_heading">Group Settings</div>
                        <div className="profile_picture">
                            <span>Group picture</span>
                            <div className="profile_picture_line">
                                <img src={serverUrl + GroupInfo.picture} alt="Picture"></img>
                                <span className="upload" onClick={() => handlePopUpContent("groupPicture", GroupInfo.link)}>Upload group picture</span>
                            </div>
                        </div>
                        <form onSubmit={this.onGroupEdit} className="mid-size main_form">
                            <div className="input_block_section">
                                <span>Group name</span>
                                <input type="text" name="name" defaultValue={GroupInfo.name} required/>
                            </div>
                            <div className="input_block_section">
                                <span>School name</span>
                                <input type="text" name="schoolName" defaultValue={GroupInfo.school_name} required/>
                            </div>
                            <div className="input_block_section">
                                <span>Invitation pass</span>
                                <span className="info">(Secret pass that will allow people to join). <br/>Note: Keep it empty to make the group open to anyone.</span>
                                <input type="text" name="code" defaultValue={GroupInfo.code} />
                            </div>
                            <input type="hidden" name="link" value={GroupInfo.link} />
                            <button className="main" type="submit">Save</button>
                        </form>
                        <br/><br/>
                        <form onSubmit={(e) => e.preventDefault()} className="mid-size main_form">
                            <div className="input_block_section">
                                <span>Group ID</span>
                                <input type="text" defaultValue={GroupInfo.link} readonly="readonly"/>
                            </div>
                            <div className="input_block_section">
                                <span>Invitation link</span>
                                <input type="text" onClick={this.copyToClipboard} ref={(groupLinkField) => this.groupLinkField = groupLinkField} defaultValue={"https://dash.jetdesk.app/join/" + GroupInfo.link} readonly="readonly"/>
                            </div>
                        </form> 
                        <br/>
                        <div className="leave" onClick={() => handlePopUpContent("leaveGroup", { smallPopUp: true, GroupLink: GroupLink })}>Leave group</div>
                    </div>
                </React.Fragment>
            );
        } else {
            return (
                <div className="group_settings_page">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

GroupSettingsPage.contextType = PopUpContext;