import React, { Component } from 'react';
// import { Link } from 'react-router-dom';

import GroupService from '../../../../services/group-service';

import PopUpContext from '../../../context/popup-context';

// import { useParams } from "react-router-dom";


export default class RatingBlock extends Component {

    state = {
        GroupService: new GroupService(),
        RatingList: null,
        GroupLink: null,
        showingRatingReloadInfo: null //to reload the block
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        const showingRating = this.props.showingRating;
        const link = this.props.link;
        if (link !== this.state.GroupLink || showingRating !== this.state.showingRatingReloadInfo) {
            this.setState({
                GroupLink: link,
                showingRatingReloadInfo: this.props.showingRating
            });
            this.state.GroupService.getGroupRating(this.props.link).then(
                res => {
                    console.log(res);
                    if (res["response"]["code"] !== undefined && res["response"]["code"] !== 777) {
                        this.setState({
                            RatingList: "off"
                        });
                    } else {
                        this.setState({
                            RatingList: res["response"]
                        });
                    }
                });
        }
    }


    render() {

        const { RatingList } = this.state;
        const { serverUrl } = this.context;

        if (RatingList !== null) {

            const RatingListBlock = () => RatingList.map((userRating, index) =>
                <div className="rating-line" key={userRating.userData.name}>
                    <span className="place">{index + 1}</span>
                    <img src={serverUrl + userRating.userData.picture} alt="User Pic" />
                    <span className="name">{userRating.userData.name}</span>
                </div>
            );

            if (RatingList !== false) {
                if (RatingList !== "off") {
                    return (
                        <div className="rating-list">
                            <RatingListBlock />
                        </div>
                    );
                } else {
                    return (
                        <div className="widget_info">
                            <div className="info-message">
                                <img className="info-illustration" src="/assets/illustration/champ-gray.svg" alt="Rating" />
                                <span>Oops, the admin has hidden<br/> group rating</span>
                            </div>
                        </div>
                    );
                }
            } else {
                return (
                    <div className="widget_info">
                        <img src="/assets/svg/circle.svg" alt="Circle" />
                        <span>No data to rate from... </span>
                    </div>
                );
            }
        } else {
            return (
                <p>Loading...</p>
            );
        }
    }
}

RatingBlock.contextType = PopUpContext;