import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './top-menu.css';

class TopMenu extends Component {

    /* 
    Needs Changes 🤷‍♂️
    Using CSS position: absolute;
    Can't update the Admin/User/Page state without refreshing other elements...
    This is a temporary decision
    */

    state = {
        currentMenuGroup: null
    }

    render() {
        const { currentGroupLink, isAdmin } = this.props;

        return (
            <div className="top-menu-absolute">
                {isAdmin ? (
                    <div className="top-menu">
                        <Link to={"/class/" + currentGroupLink}><span>Main</span></Link>
                        <Link to={"/admin/settings/" + currentGroupLink}><span>Class settings</span></Link>
                        <Link to={"/admin/manage-members/" + currentGroupLink}><span>Users</span></Link>
                        <Link to={"/tasks/" + currentGroupLink}><span>Tasks</span></Link>
                    </div>
                ) : (
                        <div className="top-menu">
                            <Link to={"/class/" + currentGroupLink}><span>Main</span></Link>
                            <Link to={"/tasks/" + currentGroupLink}><span>Tasks</span></Link>
                        </div>
                    )}
            </div>
        );
    }
}


export default TopMenu;