import React, { Component } from 'react';
import './user-page.css';

import UserService from '../../../services/user-service';

import PopUpContext from '../../context/popup-context';


export default class UserPage extends Component {

    state = {
        UserService: new UserService(),
        currentUserUsage: null,
        currentUserPlan: null
    }

    componentDidMount() {
        this.state.UserService.getUserLimit(localStorage.getItem("token")).then(
            userLimit => {
                console.log(userLimit)
                this.setState({
                    currentUserUsage: userLimit["response"]["totalFollowers"],
                    currentUserPlan: userLimit["response"]["userPlan"],
                });
            });
    }

    onProfileEdit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.UserService.editUserProfile(formData).then(
            res => {
                console.log(res);
                this.state.UserService.getUserData(localStorage.getItem("token")).then(
                    userData => {
                        console.log(userData)
                        localStorage.setItem('user', JSON.stringify(userData));
                        window.location.reload();
                    });
            }
        );
    }

    onPasswordChange = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.UserService.changeUserPassword(formData).then(
            res => {
                console.log(res);
                if (res["response"] !== undefined) {
                    alert("Password has been updated successfully, please login.");
                    localStorage.clear();
                    window.location.reload();
                } else {
                    // alert("Error! " + res["message"]);
                    const PopUpContext = this.context;
                    // console.log(PopUpContext);
                    PopUpContext.updateErrorMessage(res["message"]);
                    PopUpContext.handlePopUpContent("error");
                }
            }
        );
    }

    render() {

        const { serverUrl, logOut, handlePopUpContent } = this.context;

        const { currentUserPlan } = this.state;

        let userData = localStorage.getItem("user");
        userData = JSON.parse(userData);
        const userIcon = serverUrl + userData["response"][0]["picture"];
        const userName = userData["response"][0]["name"];
        const userEmail = userData["response"][0]["email"];

        return (
            <React.Fragment>
                <div className="goback_button" onClick={() => window.history.back()}>
                    <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>
                <div className="userpage">
                    <div className="left-side">
                        <div className="page_heading">Settings </div>
                        <div className="profile-settings">
                            <h4>Profile</h4>
                            <div className="profile_picture">
                                <span>Porfile picture</span>
                                <div className="profile_picture_line">
                                    <img src={userIcon} alt="User"></img>
                                    <span className="upload" onClick={() => handlePopUpContent("userPicture")}>Upload profile picture</span>
                                </div>
                            </div>
                            <form onSubmit={this.onProfileEdit}>
                                <div className="profile_settings_input_block">
                                    <span>Full name</span>
                                    <input type="text" name="name" defaultValue={userName} />
                                </div>
                                <div className="profile_settings_input_block">
                                    <span>Email</span>
                                    <input type="email" name="email" defaultValue={userEmail} />
                                </div>
                                <button className="main" type="submit">Save</button>
                            </form>
                            <br /> <br />
                            <h3>Security</h3>
                            <form onSubmit={this.onPasswordChange}>
                                <div className="profile_settings_input_block">
                                    <span>Old Password</span>
                                    <input type="password" name="password" placeholder="*************" />
                                    {/* <img className="profile_edit" src="/assets/svg/edit.svg" alt="Edit"></img> */}
                                </div>
                                <div className="profile_settings_input_block">
                                    <span>New Password</span>
                                    <input type="password" name="newPassword" placeholder="*************" />
                                </div>
                                <button className="main" type="submit">Update</button>
                            </form>
                        </div>
                        <br /> <br /> <br /> <br />
                        <div className="logout pc" onClick={logOut}>Log Out</div>
                    </div>
                    <div className="right-side">
                        <h4>Your plan</h4>
                        <div className="plan">
                            <div className="plan-header">
                                <div className="top-line">
                                    <h4>Personal <br />Plan</h4>
                                    <span>Forever</span>
                                </div>
                                <p>Your total limit of members in groups: {this.state.currentUserUsage}/{currentUserPlan === "free" ? 10 : null}{currentUserPlan === "enterprise" ? 250 : null}  <img src="/assets/svg/info.svg" alt="info" /></p>
                            </div>
                            <div className="plan-info">
                                <p>For everyone who wants to learn and teach productivly!</p>
                                <ul>
                                    <li><img src="/assets/svg/blue-check.svg" alt="check" />Ulimited tasks</li>
                                    <li><img src="/assets/svg/blue-check.svg" alt="check" />Ulimited lessons</li>
                                    <li><img src="/assets/svg/blue-check.svg" alt="check" />Ulimited attachments</li>
                                    <li><img src="/assets/svg/blue-check.svg" alt="check" />Visible/Invisible rating</li>
                                    <li><img src="/assets/svg/blue-check.svg" alt="check" />Group chat</li>
                                </ul>
                            </div>
                        </div>
                        <br /> <br /> <br /> <br />
                        <div className="logout mobile" onClick={logOut}>Log Out</div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

UserPage.contextType = PopUpContext;