import React, { Component } from "react";

import GroupService from '../../../services/group-service';

export default class GroupPicture extends Component {

    state = {
        GroupService: new GroupService(),
    }

    onPictureUpload = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        console.log(this.props.currentGroupLink);
        this.state.GroupService.uploadFile(localStorage.getItem("token"), this.props.currentGroupLink, document.getElementById('group-picture-form')).then(
            res => {
                console.log(res);
                window.location.reload();
            }
        );
        // this.state.GroupService.uploadFile(localStorage.getItem("token"), this.props.currentGroupLink, document.getElementById('group-picture-form'));
    }

    render() {

        return (
            <form id="group-picture-form" onSubmit={this.onPictureUpload}>
                <div className="form_content upload">
                    <div className="form_elements upload">
                        <img src="/assets/illustration/selfie.svg" className="popup-illustration" alt="selfie" />
                        <input type="file" id="profile-picture" accept="image/*" name="grouppic" />
                        <input type="hidden" name="link" value={this.props.currentGroupLink} />
                    </div>
                    <button className="main" type="submit"> Upload </button>
                    <div className="form_action upload">
                        <div className="cancel mobile-only" onClick={this.props.changePopUpState}>Cancel</div>
                    </div>
                </div>
            </form>
        );
    }
}