import React, { Component } from 'react';
import './mobile-menu.css';

// import { BrowserRouter, NavLink, Nav, Link } from 'react-router-dom';
import { NavLink, Link } from 'react-router-dom';

import { HashLink } from 'react-router-hash-link';

// import GroupService from '../../services/group-service';

class MobileMenu extends Component {

    state = {
        // GroupService: new GroupService(),
    }


    render() {
        const { handlePopUpContent } = this.props;

        const scrollWithOffset = (el, offset) => {
            const elementPosition = el.offsetTop - offset;
            window.scroll({
              top: elementPosition,
              left: 0,
              behavior: "smooth"
            });  
        }

        return (
            <div className="mobile-menu">
                <div className="menu-link">
                    <HashLink smooth to="#home" scroll={el => scrollWithOffset(el, 150)}>
                        {/* <img src="/assets/svg/mobile-home.svg" alt="Home"/> */}
                        <div className="img home"></div>
                        <span>Home</span>
                    </HashLink>
                </div>
                <div className="menu-link">
                    <HashLink smooth to="#tasks" scroll={el => scrollWithOffset(el, 50)}>
                        {/* <img src="/assets/svg/mobile-tasks.svg" alt="Tasks"/> */}
                        <div className="img tasks"></div>
                        <span>Tasks</span>
                    </HashLink>
                </div>
                <div className="menu-link">
                    <HashLink to="#rating" scroll={el => scrollWithOffset(el, 50)}>
                        {/* <img src="/assets/svg/mobile-rating.svg" alt="Rating"/> */}
                        <div className="img rating"></div>
                        <span>Rating</span>
                    </HashLink>
                </div>
                <div className="menu-link">
                    <HashLink to="#chat">
                        {/* <img src="/assets/svg/mobile-chat.svg" alt="Chat"/> */}
                        <div className="img chat"></div>
                        <span>Chat</span>
                    </HashLink>
                </div>
                <div className="menu-link">
                    <HashLink to="#files" scroll={el => scrollWithOffset(el, 50)}>
                        {/* <img src="/assets/svg/mobile-files.svg" alt="Files"/> */}
                        <div className="img files"></div>
                        <span>Files</span>
                    </HashLink>
                </div>
            </div>
        );
    }
}

export default MobileMenu;