import React, { Component } from 'react';
import '../mobile-menu/mobile-menu.css';

// import { BrowserRouter, NavLink, Nav, Link } from 'react-router-dom';
import { NavLink, Link } from 'react-router-dom';

import { HashLink } from 'react-router-hash-link';

// import GroupService from '../../services/group-service';

class MobileMenuTask extends Component {

    state = {
        // GroupService: new GroupService(),
    }


    render() {
        const { isTeacher } = this.props;

        const scrollWithOffset = (el, offset) => {
            const elementPosition = el.offsetTop - offset;
            window.scroll({
              top: elementPosition,
              left: 0,
              behavior: "smooth"
            });  
        }

        return (
            <div className="mobile-menu">
                <div className="menu-link">
                    <HashLink smooth to="#task-scroll" scroll={el => scrollWithOffset(el, 150)}>
                        {/* <img src="/assets/svg/mobile-home.svg" alt="Home"/> */}
                        <div className="img tasks"></div>
                        <span>Task</span>
                    </HashLink>
                </div>
                <div className="menu-link">
                    {isTeacher ? (
                        <HashLink smooth to="#studentswork-scroll" scroll={el => scrollWithOffset(el, 150)}>
                            {/* <img src="/assets/svg/mobile-tasks.svg" alt="Tasks"/> */}
                            <div className="img list"></div>
                            <span>Student's work</span>
                        </HashLink>
                    ) : (
                            <HashLink smooth to="#yourwork-scroll">
                                {/* <img src="/assets/svg/mobile-tasks.svg" alt="Tasks"/> */}
                                <div className="img list"></div>
                                <span>Your Work</span>
                            </HashLink>
                        )}
                </div>
            </div>
        );
    }
}

export default MobileMenuTask;