import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './header.css';

class Header extends Component {

    state = {
        currentMenuGroup: null
    }

    render() {
        let userData = localStorage.getItem("user");
        userData = JSON.parse(userData);
        // console.log(userData);
        const { serverUrl } = this.props;
        const userIcon = serverUrl + userData["response"][0]["picture"];

        const { openHideMenu, currentGroupLink } = this.props;

        return (
            <div className="header">
                <div className="mobile-menu-icon" onClick={openHideMenu}>
                    <img src="/assets/svg/mobile-menu.svg" alt="Menu" />
                </div>
                <Link to="/">
                    <img className="logo" src="/assets/svg/logo.svg" alt="JetDesk" />
                </Link>
                <Link className="mobile-logo" to="/">
                    <img className="logo-mobile-icon" src="/assets/svg/logo.svg" alt="JetDesk" />
                    <span>JetDesk</span>
                </Link>

                <div className="header-right-side">
                    {/* {currentGroupLink  ? (
                        <div className="top-menu">
                            <Link to={"/class/" + currentGroupLink}><span>Main</span></Link>
                            <Link to={"/tasks/" + currentGroupLink}><span>Tasks</span></Link>
                        </div>
                    ) : null} */}

                    <Link to="/profile/">
                        <div className="user-icon">
                            <img src={userIcon} alt="User" />
                        </div>
                    </Link>
                </div>
            </div>
        );
    }
}


export default Header;