import React, { Component } from "react";

export default class Error extends Component {
    render() {
        return (
            <div className="error_block">
                <img className="popup-illustration" src="/assets/illustration/error.svg" alt="Error" />
                <p className="error_text">{this.props.errorMessage}</p>
                <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                {/* <button className="main" onClick={this.props.changePopUpState}>Okay</button> */}
            </div>
        );
    }
}