import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './group-page.css';

import GroupService from '../../../services/group-service';

import PopUpContext from '../../context/popup-context';

import ClockBlock from './home-blocks/clock-block';
import TasksBlock from './home-blocks/tasks-block';
import RatingBlock from './home-blocks/rating-block';
import FilesBlock from './home-blocks/files-block';
import LessonsBlock from './home-blocks/lessons-block';
import ChatBlock from './home-blocks/chat-block';

import TopMenu from '../../top-menu';

import MobileMenu from '../../mobile-menu';

// import { useParams } from "react-router-dom";


export default class GroupPage extends Component {

    state = {
        GroupService: new GroupService(),
        GroupLink: null,
        GroupInfo: null,
        UserRole: null,
        showingGroupMenu: false,
        showingRating: null,
        refreshChat: false,
    }


    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        let link = this.props.match.params.link;
        if (this.state.GroupLink !== link) {
            console.log("Update")
            this.setState({
                GroupLink: link,
                showingGroupMenu: false
            });

            // if (this.context.currentGroupLink !== link) {
            //     this.context.updateCurrentGroupLink(link);
            // }

            this.state.GroupService.getGroupData(link).then(
                res => {
                    console.log(res);
                    if (res["response"]) {
                        this.state.GroupService.getRole(link).then(
                            roleRes => {
                                // console.log(roleRes);
                                this.setState({
                                    UserRole: roleRes["response"],
                                    // GroupInfo: res["response"][0]
                                });
                                this.setState({
                                    GroupInfo: res["response"][0]
                                });
                            });
                    } else {
                        console.log("Not found");
                        window.location = "/";
                    }
                }
            );
        }
    }


    showHideGroupMenu = () => {
        this.setState({
            showingGroupMenu: !this.state.showingGroupMenu
        });
    }


    hideGroupRating = () => {
        let link = this.props.match.params.link;
        this.state.GroupService.hideGroupRating(link).then(
            res => {
                console.log(res);
                // window.location.reload();
                this.setState({
                    showingRating: false, //just to refresh the block
                })
            });
    }

    showGroupRating = () => {
        let link = this.props.match.params.link;
        this.state.GroupService.showGroupRating(link).then(
            res => {
                console.log(res);
                this.setState({
                    showingRating: true, //just to refresh the block
                })
            });
    }

    refreshChat = () => {
            this.setState({
                refreshChat: !this.state.refreshChat,
            });
    }


    render() {

        const { GroupInfo, UserRole, GroupLink, showingGroupMenu, showingRating, refreshChat } = this.state;

        const { handlePopUpContent, serverUrl } = this.context;

        // console.log("GroupLink: " + GroupLink);
        // console.log("UserRole: " + UserRole);

        console.log(showingGroupMenu);

        if (GroupInfo !== null) {
            return (
                <React.Fragment>
                    {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} />}

                    <div className="grouppage" id="home">
                        {showingGroupMenu ? <div className="groupmenu_clicker_overlay" onClick={this.showHideGroupMenu}></div> : null}

                        <ClockBlock />

                        <div className="page_heading">
                            {GroupInfo.name} {UserRole === "admin" ? <Link to={"/admin/settings/" + GroupLink}><img className="settings_icon" src="/assets/svg/settings.svg" alt="Settings" /></Link> : (
                                <div className="actions_block">
                                    <img className="more_icon" onClick={this.showHideGroupMenu} src="/assets/svg/more.svg" alt="More" />
                                    {showingGroupMenu ? <div className="group-menu" onClick={() => handlePopUpContent("leaveGroup", { smallPopUp: true, GroupLink: GroupLink })}>Leave class</div> : null}
                                </div>
                            )}
                        </div>
                        <div className="school_name">
                            <img src={serverUrl + GroupInfo.picture} alt={GroupInfo.school_name} />
                            {GroupInfo.school_name}
                        </div>

                        <div className="grouppage_blocks">

                            <div className="block tasks_block" id="tasks">
                                <div className="block_heading">
                                    Tasks {UserRole === "admin" || UserRole === "teacher" ? <React.Fragment> <img onClick={() => handlePopUpContent("newTask", GroupLink)} className="plus_icon" src="/assets/svg/+.svg" alt="+" /> <Link className="icon_link" to={"/tasks/" + GroupLink}><img className="settings_block_icon" src="/assets/svg/settings.svg" alt="Settings" /> </Link></React.Fragment> : <Link className="icon-link" to={"/tasks/" + GroupLink}><img className="forward_icon" src="/assets/svg/forward.svg" alt="Forward" /> </Link>}
                                </div>
                                <div className="widget_box">

                                    <TasksBlock link={GroupLink} />

                                    <div className="gradient"></div>
                                </div>
                            </div>

                            <div className="block">
                                <div className="block_heading">
                                    Lessons {UserRole === "admin" || UserRole === "teacher" ? <Link className="icon_link" to={"/admin/manage-lessons/" + GroupLink}><img className="settings_block_icon" src="/assets/svg/settings.svg" alt="Settings" /> </Link> : null}
                                </div>
                                <div className="widget_box calendar">
                                    <LessonsBlock link={GroupLink} />
                                </div>
                            </div>

                        </div>
                        <div className="grouppage_blocks second">

                            <div className="block" id="rating">
                                <div className="block_heading">
                                    Rating {UserRole === "admin" || UserRole === "teacher" ? <RatingAdminAction hideGroupRating={this.hideGroupRating} showGroupRating={this.showGroupRating} /> : null} {UserRole === "admin" || UserRole === "teacher" ? <Link className="icon_link" to={"/admin/manage-members/" + GroupLink}><img className="settings_block_icon" src="/assets/svg/settings.svg" alt="Settings" /> </Link> : null}
                                </div>
                                <div className="widget_box bottom_scroll_box">
                                    <RatingBlock link={GroupLink} showingRating={this.state.showingRating} />
                                    <div className="bottom-gradient"></div>
                                </div>
                            </div>

                            <div className="block" id="files">
                                <div className="block_heading">
                                    Attachments {UserRole === "admin" || UserRole === "teacher" ? <img onClick={() => handlePopUpContent("newFile", GroupLink)} className="plus_icon" src="/assets/svg/+.svg" alt="+" /> : null}
                                </div>
                                <div className="widget_box bottom_scroll_box">
                                    <FilesBlock link={GroupLink} />
                                    <div className="bottom-gradient"></div>
                                </div>
                            </div>

                            <div className="block" id="chat">
                                <div className="block_heading">
                                    Chat <img className="refresh_icon" onClick={() => this.refreshChat()} src="/assets/svg/refresh.svg" alt="Refresh" />
                                </div>
                                <div className="widget_box chat">
                                    <ChatBlock link={GroupLink} refreshChat={refreshChat} />
                                </div>
                            </div>

                        </div>
                    </div>
                    <MobileMenu />
                </React.Fragment>
            );

            function RatingAdminAction(props) {
                if (showingRating === null) {
                    if (GroupInfo.rating === 1) {
                        return <img onClick={props.hideGroupRating} className="plus_icon" src="/assets/svg/eye.svg" alt="Show" />;
                    } else {
                        return <img onClick={props.showGroupRating} className="plus_icon" src="/assets/svg/eye-hide.svg" alt="Hide" />;
                    }
                } else {
                    if (showingRating) {
                        return <img onClick={props.hideGroupRating} className="plus_icon" src="/assets/svg/eye.svg" alt="Show" />;
                    } else {
                        return <img onClick={props.showGroupRating} className="plus_icon" src="/assets/svg/eye-hide.svg" alt="Hide" />;
                    }
                }
            }
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

GroupPage.contextType = PopUpContext;