import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './all-tasks-page.css';

import TopMenu from '../../top-menu';

import TaskService from '../../../services/task-service';
import GroupService from '../../../services/group-service';

import PopUpContext from '../../context/popup-context';


// import { useParams } from "react-router-dom";


export default class TaskPage extends Component {

    state = {
        TaskService: new TaskService(),
        GroupService: new GroupService(),
        UserRole: null,
        GroupInfo: null,
        GroupLink: null,
        TasksList: null,
        UnDoneTasks: null
    }

    componentDidMount() {
        const groupLink = this.props.match.params.link;

        // if (this.context.currentGroupLink !== groupLink) {
        //     this.context.updateCurrentGroupLink(groupLink);
        // }

        this.state.GroupService.getGroupData(groupLink).then(
            res => {
                console.log(res);
                if (res["response"] !== false ) {
                    this.state.TaskService.getTasksWithMyHomework(groupLink).then(
                        taskRes => {
                            console.log(taskRes["response"]);

                            this.state.TaskService.getAdminRole(groupLink).then(
                                roleRes => {
                                    console.log(roleRes);
                                    this.setState({
                                        GroupInfo: res["response"][0],
                                        TasksList: taskRes["response"],
                                        UserRole: roleRes["response"],
                                        GroupLink: res["response"][0]["link"]
                                    });
                                });
                            // this.setState({
                            //                 GroupInfo: res["response"][0],
                            //                 TasksList: taskRes["response"]
                            //             });
                        });
                } else {
                    window.location = "/";
                }
            });

    }

    componentDidUpdate(){
        console.log("Update");

        const { TasksList, UnDoneTasks } = this.state;

        if(UnDoneTasks === null){
            if(TasksList){
            const unDone = TasksList.filter(task => task.homework === undefined);

            this.setState({
                UnDoneTasks: unDone.length
            })
        } else {
            this.setState({
                UnDoneTasks: 0
            })
        }
        }
    }

    // componentDidUpdate(prevProps, prevState) {
    //     if (this.state.UserRole === "admin") {
    //         if (prevState.AllHomeWorks === this.state.AllHomeWorks) {
    //             this.state.TaskService.getAllHomework(this.state.TaskInfo.task_link, this.state.TaskInfo.group_link).then(
    //                 res => {
    //                     console.log(res);
    //                     this.setState({
    //                         AllHomeWorks: res["response"]
    //                     });
    //                 }
    //             );
    //         }
    //     }
    // }


    render() {

        const { GroupInfo, UserRole, TasksList, UnDoneTasks, GroupLink } = this.state;

        const { reformatDate } = this.context;

        const userElements = {
            display: UserRole === "student" ? 'block' : 'none'
        }

        const adminElements = {
            display: UserRole === "admin" || UserRole === "teacher" ? 'block' : 'none'
        }


        if (GroupInfo !== null) {

            // console.log(GroupInfo);

            const TaskStatusOptions = (status) => {
                switch(status){
                    case "open":
                        return <div style={userElements} className="td status blue">Sent</div>
                    case "checked":
                        return <div style={userElements} className="td status green">Done</div>
                }
            }

            const TaskStatusOptionsMobile = (status) => {
                switch(status){
                    case "open":
                        return <div style={userElements} className="td status-m blue">Sent</div>
                    case "checked":
                        return <div style={userElements} className="td status-m green">Done</div>
                }
            }

            const AllTasksBlock = () => TasksList.map((task) =>
                <Link to={"/task/" + task.task_link} key={task.task_link}>
                <div className={"tasks-row " + task.color}>
                    <div className="td name">{task.name}</div>
                    {/* <div className="table_space" style={adminElements}></div> */}
                    <div className="td topic">{task.topic}</div>
                    <div className="td deadline">{reformatDate(task.deadline.substr(0, 10))}</div>
                    {task.homework ? TaskStatusOptions(task.homework.status) : <div style={userElements} className="td status red"> Not sent </div> } 
                    {task.homework ? <div style={userElements} className="td mark">{task.homework.teacher_mark}</div> : <div style={userElements} className="td mark"> — </div> } 
                    <div className="table_space" style={adminElements}></div>
                    <div className="td published">{reformatDate(task.created_date.substr(0, 10))}</div>

        {task.homework ? TaskStatusOptionsMobile(task.homework.status) : <div className="td published-m">{reformatDate(task.created_date.substr(0, 10))}</div> }
                    <div className="td short_description_mobile">{task.comment.substr(0, 50)}</div>
                </div>
                </Link>
            )


            const ErrorScreen = (props) => {
                return(
                    <div className="error-screen">
                        <img src="/assets/illustration/desk.svg" alt="Desk" />
                        <p>The teacher hasn't posted any tasks yet. You can rest!</p>
                    </div>
                );
            }

            return (
                <React.Fragment>
                    {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} /> }

                    <div className="goback_button" onClick={() => window.history.back()}>
                        <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>
                    
                    <div className="alltasks-page action-space">
                        <div className="page_heading">Tasks</div>
                        {UnDoneTasks ? <div className="undone" style={userElements}>Undone tasks: {UnDoneTasks}</div> : null}
                        <div className="tasks-column-names">
                            <span>Name</span>
                            {/* <div className="table_space" style={adminElements}></div> */}
                            <span>Topic</span>
                            <span>Deadline</span>
                            <span style={userElements}>Status</span>
                            <span style={userElements}>Mark</span>
                            <div className="table_space" style={adminElements}></div>
                            <span>Created</span>
                        </div>
                        <div className="tasks-column-rows">
                        {TasksList ? <AllTasksBlock /> : <ErrorScreen />}
                        </div>
                    </div>
                </React.Fragment>
            );
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

TaskPage.contextType = PopUpContext;