import React, { Component } from "react";

import GroupService from '../../../services/group-service';

export default class MemberInfo extends Component {

    state = {
        GroupService: new GroupService(),
        MemberInfo: null,
    }

    componentDidMount() {

        let memberId = this.props.currentGroupLink.UserId;
        let groupLink = this.props.currentGroupLink.GroupLink;

        this.state.GroupService.getMemberInfo(groupLink, memberId).then(
            res => {
                console.log(res);
                if (res["code"] === undefined) {
                    this.setState({
                        MemberInfo: res["response"],
                    });
                } else {
                    window.location = "/";
                }
            });
    }


    showRoleMenu = (num) => {
        document.getElementById("roleMenuInfo" + num).style.display = "block";

        function checkForClick(event) {
            let isClickInside = document.getElementById("roleMenuInfo" + num).contains(event.target);

            if (!isClickInside) {
                document.getElementById("roleMenuInfo" + num).style.display = "none";
                document.removeEventListener("click", checkForClick, false);
            }
        }

        document.addEventListener('click', checkForClick);
    }


    setTeacher = (userId) => {
        this.state.GroupService.setTeacher(this.props.currentGroupLink.GroupLink, userId).then(
            res => {
                if(res["response"]["code"] !== "100"){
                    alert("To many teachers for this group...");
                } else{
                    window.location.reload(); 
                }
            });
    }

    setTeacherToStudent = (userId) => {
        this.state.GroupService.setTeacherToStudent(this.props.currentGroupLink.GroupLink, userId).then(
            res => {
                console.log(res);
                window.location.reload();
            });
    }

    render() {

        const { MemberInfo } = this.state;
        const { handlePopUpContent } = this.props;


        if (MemberInfo !== null) {
            return (
                <div className="popup_form">
                    <h4>Information</h4>
                    <form>
                        <div className="form_elements">
                            <div className="info_block">
                                <span className="label">Full Name</span>
                                <span className="info">{MemberInfo.userData.name}</span>
                            </div>
                            <div className="info_block">
                                <span className="label">Role</span>
                                {/* <span className="info">{MemberInfo.userRole}</span> */}

                                <span className="info"> {MemberInfo.userRole} {MemberInfo.userRole !== "admin" ? <img onClick={() => this.showRoleMenu(MemberInfo.id)} className="down-icon" src="/assets/svg/down.svg" alt="Select" /> : null} </span>

                                <div className="role-menu" id={"roleMenuInfo" + MemberInfo.id}>
                                    {MemberInfo.userRole === "teacher" ? <React.Fragment><div className="line blue"><img src="/assets/svg/check.svg" alt="Check" /> Teacher</div><div className="line" onClick={() => this.setTeacherToStudent(MemberInfo.id)}>Student</div></React.Fragment> : <React.Fragment><div className="line blue"><img src="/assets/svg/check.svg" alt="Check" /> Student</div><div className="line" onClick={() => this.setTeacher(MemberInfo.id)}>Teacher</div></React.Fragment>}
                                </div>

                            </div>
                            <div className="info_block">
                                <span className="label">Mark</span>
                                <span className="info">{MemberInfo.totalUserMark}</span>
                            </div>
                            {MemberInfo.userRole !== "admin" ? <div className="delete-action" onClick={() => handlePopUpContent("deleteMember", { smallPopUp: true, GroupLink: this.props.currentGroupLink.GroupLink, UserId: MemberInfo.id })}>Remove member</div> : null }
                        </div>
                        {/* <div className="bottom-gradient"></div> */}
                        <div className="form_action">
                            <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                        </div>
                    </form>
                </div>
            );
        } else {
            return (
                <p>Member can't be loaded...</p>
            )
        }
    }
}