import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";


import './app.css';

//context
import PopUpContext from '../context/popup-context';

//layouts
// import PopUp from '../popup';
import Auth from '../pages/layouts/auth';
import SignIn from '../pages/layouts/signin';
import SignUp from '../pages/layouts/signup';
import DefaultLayout from '../pages/layouts/default';

//pages
import HomePage from '../pages/home';
import UserProfile from '../pages/user';
import GroupPage from '../pages/group';
import TaskPage from '../pages/task';
import AllTasksPage from '../pages/all-tasks';
import GroupSettingsPage from '../pages/admin/group-settings-page';
import HomeWorkPage from '../pages/admin/homework-page';
import ManageLessonsPage from '../pages/admin/manage-lessons';
import JoinClassPage from '../pages/join';
import ManageMembersPage from '../pages/admin/manage-members';
import Restore from '../pages/layouts/restore/restore';
import Password from '../pages/layouts/restore/pass';

export default class App extends Component {

  state = {
    isLogedIn: false,
    token: null,
    showingPopUp: false,
    popUpContent: null,
    errorMessage: null,
    currentGroupLink: null,
    // serverUrl: 'http://localhost:5000/',
    serverUrl: 'https://api.jetdesk.app/',
  };


  componentDidMount() {
    if (localStorage.getItem('token') !== null) {
      this.logIn();
    }
  }


  logOut = () => {
    localStorage.clear();
    this.setState({
      token: null,
      isLogedIn: false
    });
  }

  logIn = () => {
    this.setState({
      token: localStorage.getItem('token'),
      isLogedIn: true
    });
  }

  updateErrorMessage = (msg) => {
    this.setState({
      errorMessage: msg
    });
  }

  changePopUpState = () => {
    this.setState({
      showingPopUp: !this.state.showingPopUp
    });
    document.body.style.overflow = 'unset';
    document.querySelector('body').style.touchAction = "auto"; //enable scroll
  }

  handleTouchMove(e) {
    e.preventDefault();
  }


  handlePopUpContent = (name, currentGroupLink) => {
    console.log("handlePopUpContent: " + name);

    if (currentGroupLink === undefined) {
      currentGroupLink = null;
    }
    console.log("currentGroupLink: " + currentGroupLink);

    document.body.style.overflow = 'hidden';

  //   window.addEventListener("scroll", (e) => {
  //     e.preventDefault();
  //     window.scrollTo(0, 0);
  // });

  document.querySelector('body').style.touchAction = "none";  //disable scroll


    const { showingPopUp, popUpContent } = this.state;
    if (popUpContent === name) {
      if (!showingPopUp) {
        this.setState({ showingPopUp: true, popUpContent: name, currentGroupLink: currentGroupLink });
      } else {
        this.setState({ showingPopUp: false, popUpContent: null, currentGroupLink: null });
      }
    } else {
      this.setState({ showingPopUp: true, popUpContent: name, currentGroupLink: currentGroupLink });
    }

  };

  reformatDate = (dateStr) => {
    let dArr = dateStr.split("-");  // ex input "2010-01-18"
    // return dArr[2] + "/" + dArr[1] + "/" + dArr[0].substring(2); //ex out: "18/01/10"
    return dArr[1] + "/" + dArr[2] + "/" + dArr[0]; //ex out: 04/23/20
  }


  updateCurrentGroupLink = (link) => {
    console.log("updateCurrentGroupLink: " + link);
    // if(this.state.currentGroupLink !== link){
    this.setState({
      currentGroupLink: link
    });
    // } 
  }


  render() {
    const { isLogedIn, showingPopUp, popUpContent, errorMessage, currentGroupLink, serverUrl } = this.state;
    const { logOut, changePopUpState, handlePopUpContent, updateErrorMessage, updateCurrentGroupLink, reformatDate } = this;

    const PopUpProviderProps = {
      logOut,
      showingPopUp,
      popUpContent,
      changePopUpState,
      handlePopUpContent,
      errorMessage,
      updateErrorMessage,
      currentGroupLink,
      updateCurrentGroupLink,
      reformatDate,
      serverUrl,
    }


    function RouteWrapper({
      component: Component,
      layout: Layout,
      ...rest
    }) {
      return (
        <Route {...rest} render={(props) =>
          <PopUpContext.Provider value={PopUpProviderProps}>
            <Layout {...props}>
              <Component {...props} />
            </Layout>
            {/* {showingPopUp ? <PopUp /> : null} */}
          </PopUpContext.Provider>
        } />
      );
    }


    {/* <Route path="/" exact>
              <PopUpContext.Provider value={PopUpProviderProps}>
                <DefaultLayout>
                  <HomePage />
                </DefaultLayout>
              </PopUpContext.Provider>
    </Route> */}

    {/* <Route path="/class/:link" render={(props) => <PopUpContext.Provider value={PopUpProviderProps}><DefaultLayout><GroupPage {...props} /> </DefaultLayout></PopUpContext.Provider>} exact></Route> */ }

    if (isLogedIn) {
      // if(localStorage.getItem('token') !== null){
      return (
        <Router>
          <Switch>
            <RouteWrapper path="/" exact component={HomePage} layout={DefaultLayout} />
            <RouteWrapper path="/profile" exact component={UserProfile} layout={DefaultLayout} />
            <RouteWrapper path="/class/:link" exact component={GroupPage} layout={DefaultLayout} />
            <RouteWrapper path="/task/:link" exact component={TaskPage} layout={DefaultLayout} />
            <RouteWrapper path="/tasks/:link" exact component={AllTasksPage} layout={DefaultLayout} />
            <RouteWrapper path="/join/:link" exact component={JoinClassPage} layout={DefaultLayout} />

            <RouteWrapper path="/admin/settings/:link" exact component={GroupSettingsPage} layout={DefaultLayout} />
            <RouteWrapper path="/admin/homework/:link" exact component={HomeWorkPage} layout={DefaultLayout} />
            <RouteWrapper path="/admin/manage-lessons/:link" exact component={ManageLessonsPage} layout={DefaultLayout} />
            <RouteWrapper path="/admin/manage-members/:link" exact component={ManageMembersPage} layout={DefaultLayout} />
            <Redirect to="/" />
            {localStorage.getItem('token') !== null ? <Redirect to="/" /> : null }
          </Switch>
        </Router>
      );
    } else {
      return (
        <Router>
          <Switch>
            <Route path="/signin" exact>
              <SignIn logIn={this.logIn} />
            </Route>
            <Route path="/signup" exact>
              <SignUp logIn={this.logIn} />
            </Route>
            <Route path="/restore" exact>
              <Restore />
            </Route>
            <Route path="/pass" exact>
              <Password />
            </Route>
            {localStorage.getItem('token') === null ? <Redirect to="/signin" /> : null }
            {/* <Redirect to="/signin" /> */}
          </Switch>
        </Router>
      );

      // return <Auth logIn={this.logIn} />
    }


  }
}
