import React, { Component } from "react";

import TaskService from '../../../services/task-service';

export default class EditTask extends Component {

    state = {
        TaskService: new TaskService(),
        TaskInfo: null,
        taskColor: null,
    }

    componentDidMount() {

        let taskLink = this.props.currentGroupLink; //it's actually a Task Link
        console.log(taskLink); 

        this.state.TaskService.getSingleTask(taskLink).then(
            res => {
                console.log(res);
                if (res["code"] === undefined) {
                    this.setState({
                        TaskInfo: res["response"][0],
                        taskColor: res["response"][0]["color"]
                    });
                    this.firstInput.focus();
                } else {
                    window.location = "/";
                }
            });
    }


    updateNewTaskColor = (color) => {
        this.setState({
            taskColor: color
        });
    }


    onTaskEdit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        console.log(formData)

        this.state.TaskService.editTask(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    // alert("Task edited!");
                    window.location = "/task/" + res["response"]["link"];
                } else {
                    alert("Error! Check console");
                }
            }
        );
    }

    render() {

        const { TaskInfo } = this.state;

        let today = new Date();
        today = today.toISOString().substring(0, 10);

        if (TaskInfo !== null) {
        return (
            <div className="popup_form">
                <h4>Edit task</h4>
                <form onSubmit={this.onTaskEdit}>
                <div className="form_elements">
                    <div className="form_input_section">
                        <input type="text" ref={(input) => { this.firstInput = input; }} name="name" placeholder="Task name (ex: Write a Poem)" defaultValue={TaskInfo.name} required />
                        <span className="label">Task name</span>
                    </div>
                    <div className="form_input_section">
                        <input type="text" name="topic" placeholder="(Could be any subject)" defaultValue={TaskInfo.topic} required />
                        <span className="label">Task subject</span>
                    </div>
                    <div className="form_input_section">
                        <input type="date" id="start" min={today} name="deadline" placeholder="Select the deadline date" defaultValue={TaskInfo.deadline.substring(0, 10)} required />
                        <span className="label">Task deadline</span>
                    </div>
                    <div className="form_input_section">
                        <input type="number" name="maxMark" placeholder="Ex.: 100" defaultValue={TaskInfo.max_mark} required />
                        <span className="label">Max Mark</span>
                    </div>
                    <div className="form_input_section">
                        <input type="text" name="comment" placeholder="(Task description)" defaultValue={TaskInfo.comment} required />
                        <span className="label">Comment</span>
                    </div>
                    {/* <div className="form_input_section">
                        <input type="text" name="color" placeholder="(blue, green, red, pink, yellow)" required/>
                        <span className="label">Color</span>
                    </div> */}
                    <div className="form_radio_color_section">
                        <span>Select a color tag for the task</span>
                        <span>(Current color is {this.state.taskColor})</span>

                        <input type="radio" name="color_picker" id="color_1" value="blue" />
                        <label htmlFor="color_1" onClick={() => this.updateNewTaskColor("blue")} className="small_circle blue"></label>

                        <input type="radio" name="color_picker" id="color_2" value="green" />
                        <label htmlFor="color_2" onClick={() => this.updateNewTaskColor("green")} className="small_circle green"></label>

                        <input type="radio" name="color_picker" id="color_3" value="red" />
                        <label htmlFor="color_3" onClick={() => this.updateNewTaskColor("red")} className="small_circle red"></label>

                        <input type="radio" name="color_picker" id="color_4" value="pink" />
                        <label htmlFor="color_4" onClick={() => this.updateNewTaskColor("pink")} className="small_circle pink"></label>

                        <input type="radio" name="color_picker" id="color_5" value="yellow" />
                        <label htmlFor="color_5" onClick={() => this.updateNewTaskColor("yellow")} className="small_circle yellow"></label>
                    </div>

                    <input type="hidden" name="link" value={TaskInfo.group_link} />
                    <input type="hidden" name="taskLink" value={TaskInfo.task_link} />
                    <input type="hidden" name="color" value={this.state.taskColor} />
                    <div className="bottom-gradient"></div>
                    </div>
                    <div className="form_action">
                        <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                        <button className="main" type="submit">Edit</button>
                    </div>
                </form>
            </div>
        );
                } else{
                    return(
                        <p>Task can't be loaded...</p>
                    )
                }
    }
}