import React, { Component } from 'react';
// import { Link } from 'react-router-dom';

import GroupService from '../../../../services/group-service';
import PopUpContext from '../../../context/popup-context';

import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import listPlugin from '@fullcalendar/list';
import interactionPlugin from "@fullcalendar/interaction"; // needed for dayClick
// import { useParams } from "react-router-dom";


export default class LessonsBlock extends Component {

    state = {
        GroupService: new GroupService(),
        LessonsList: null,
        GroupLink: null,
        showingEventBlock: false,
        selectedEvent: null
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        const link = this.props.link;
        if (link !== this.state.GroupLink) {
            this.setState({
                GroupLink: link
            });
            this.state.GroupService.getLessons(this.props.link).then(
                res => {
                    console.log(res);
                    this.setState({
                        LessonsList: res["response"]
                    });
                });
        }
    }


    hideEventInfo = () => {
        this.setState({
            showingEventBlock: false
        })
    }

    handleDateClick = (arg) => { // bind with an arrow function
        console.log(arg);
        // alert(arg.dateStr)
    }

    handleEventClick = (arg) => { // bind with an arrow function
        console.log(arg);
        // console.log(arg.event._def)
        this.setState({
            selectedEvent: arg.event,
            showingEventBlock: true
        })
    }

    setClipboard = (value) => {
        let tempInput = document.createElement("input");
        tempInput.style = "position: absolute; left: -1000px; top: -1000px";
        tempInput.value = value;
        document.body.appendChild(tempInput);
        tempInput.select();
        document.execCommand("copy");
        document.body.removeChild(tempInput);
        alert("Copied!")
    }

    render() {

        const { showingEventBlock, selectedEvent, LessonsList, GroupLink } = this.state;

        if (LessonsList !== null) {

            let eventsArr = [];

            for (let i = 0; i < LessonsList.length; i++) {
                eventsArr.push({
                    daysOfWeek: [LessonsList[i]["days_of_week"]],
                    startTime: LessonsList[i]["start_time"],
                    title: LessonsList[i]["title"],
                    description: LessonsList[i]["description"],
                    extendedProps: { lesson_url: LessonsList[i]["lesson_url"] }
                })
            }

            console.log(eventsArr);

            const EventInfoBlock = (props) => {
                console.log(props);
                if (showingEventBlock) {
                    const info = props.eventInfo._def;
                    const dateObj = props.eventInfo._instance.range.start;
                    let date = new Date(dateObj);
                    let dateOffset = date.getTimezoneOffset();
                    dateOffset = dateOffset.toString().substring(1);
                    date.setMinutes(date.getMinutes() - dateOffset);
                    const day = date.getDay();
                    const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
                    const month = monthNames[date.getMonth()];
                    const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
                    const dayName = days[date.getDay()];
                    const hours = date.getHours();
                    let minutes = date.getMinutes();
                    console.log(minutes.length);
                    if (minutes.toString().length === 1) {
                        minutes = "0" + minutes
                    }

                    const lessonsActionsStyle = {
                        display: info.extendedProps.lesson_url !== null ? "flex" : "none",
                    }

                    return (
                        <div className="event-info">
                            <div className="top-line">
                                <div className="name">
                                    <div className="circle"></div> {info.title}
                                </div>
                                <img onClick={this.hideEventInfo} className="event-info-close" src="/assets/svg/blue-x.svg" />
                            </div>
                            <div className="date">
                                <div className="blue">{month} {day}, {hours}:{minutes} </div>
                                {dayName}
                            </div>
                            {/* <span className="hours">{hours}:{minutes}</span> */}
                            <div className="event-description">
                                <p>{info.extendedProps.description}</p>
                                <div className="bottom-gradient"></div>
                            </div>
                            <div className="actions" style={lessonsActionsStyle}>
                                <a rel="noopener noreferrer" href={`${info.extendedProps.lesson_url}`} target="_blank">
                                    <button className="main">Join meeting</button>
                                </a>
                                <div className="copy-link" onClick={() => this.setClipboard(info.extendedProps.lesson_url)}>
                                    <img src="/assets/svg/copy.svg" />
                                    Copy link
                                 </div>
                            </div>
                        </div>
                    )
                } else {
                    return null;
                }
            }

            return (

                <div className="calendar-wrapper">
                    <FullCalendar
                        plugins={[dayGridPlugin, listPlugin, interactionPlugin]}
                        dateClick={this.handleDateClick}
                        // eventContent={renderEventContent}
                        eventClick={this.handleEventClick}
                        timezone="local"
                        ignoreTimezone={false}
                        initialView="listMonth" //dayGridMonth, listMonth
                        weekends={true}
                        // events={[
                        //     // { title: 'event 1', date: '2020-10-09 20:30', description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum... Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum...', extendedProps: { lesson_url: "http://finelabs.tech" } }, //url: 'http://google.com/' 
                        //     // { title: 'event 2', date: '2020-10-09 15:01', description: 'This is a cool event', },
                        //     // { title: 'event 3', date: '2020-10-19 15:40', description: 'This is a cool event', },
                        //     // { title: 'event 4', date: '2020-10-19 16:40', description: 'This is a cool event', },
                        //     // { title: 'event 5', date: '2020-10-19 17:40', description: 'This is a cool event', },
                        //     // { title: 'event 6', date: '2020-10-19 18:00', description: 'This is a cool event', },
                        //     // { title: "Arthur's Birthday!", date: '2020-10-20 12:00', description: 'Dope', }
                        //     {
                        //         daysOfWeek: [ '3' ], // these recurrent events move separately
                        //         startTime: '11:00',
                        //         title: 'some event',
                        //         description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum... Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum...', extendedProps: { lesson_url: "http://finelabs.tech" }
                        //       },
                        //       {
                        //         daysOfWeek: [ '5' ], // these recurrent events move separately
                        //         startTime: '16:30',
                        //         title: 'Friday Night Cafe',
                        //         description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum... Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringillaurna, porttitor rhoncus dolor purus non enim praesent elementum...', extendedProps: { lesson_url: "http://finelabs.tech" }
                        //       }
                        // ]}
                        events={eventsArr}

                    />
                    <EventInfoBlock eventInfo={selectedEvent} />
                </div>
            );

        } else {
            return (
                <p>Loading...</p>
            );
        }
    }
}


function renderEventContent(eventInfo) {
    return (
        <>
            <b>{eventInfo.timeText}</b>
            <i>{eventInfo.event.title}</i>
        </>
    )
}

LessonsBlock.contextType = PopUpContext;