import React, { Component } from "react";

import './join-class.css';

import GroupService from '../../../services/group-service';

import PopUpContext from '../../context/popup-context';

export default class JoinClassPage extends Component {

    state = {
        GroupLink: null,
        GroupService: new GroupService(),
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        let link = this.props.match.params.link;
        if (this.state.GroupLink !== link) {
            this.setState({
                GroupLink: link,
            });

        }
    }

    onGroupJoin = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.GroupService.joinGroup(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location = "/class/" + res["response"]["link"];
                } else {
                    // alert(res["response"]["message"]);
                    this.context.updateErrorMessage(res["response"]["message"]);
                    this.context.handlePopUpContent("error");
                }
            }
        );
    }

    render() {

        const { GroupLink } = this.state;

        return (
            <div className="join-class">
                <div className="page_heading">Join group via link</div>
                <div className="two-sides">
                    <div className="left-page">
                        <form onSubmit={this.onGroupJoin}>
                            <div className="margin-top"></div>
                            <div className="profile_settings_input_block">
                                <span>Group ID</span>
                                <input type="text" defaultValue={GroupLink} readonly="readonly" />
                            </div>
                            <div className="profile_settings_input_block">
                                <span>Enter invitation pass (if required)</span>
                                <input type="text" name="code" placeholder="Invitation pass" />
                            </div>

                            <input type="hidden" name="link" defaultValue={GroupLink} />
                            <div className="margin-top-small"></div>
                            <div className="form_action">
                                <button className="main" type="submit">Join</button>
                            </div>
                        </form>
                    </div>
                    <div className="right-page flex-center">
                        <img src="/assets/illustration/enter.svg" className="join-illustration" alt="Enter" />
                    </div>
                </div>
            </div>
        );
    }
}

JoinClassPage.contextType = PopUpContext;