import React, { Component } from 'react';
import './popup.css';

import AddJoin from '../popup-content/add-join';
import AddGroupPopupContent from '../popup-content/add-join-next/add-group-popup-content';
import JoinGroupPopupContent from '../popup-content/add-join-next/join-group-popup-content';
import UserPicture from '../popup-content/user-picture';
import NewTask from '../popup-content/new-task/new-task';
import Error from '../popup-content/error';

import PopUpContext from '../context/popup-context';
import EditTask from '../popup-content/edit-task/edit-task';
import RateHomework from '../popup-content/homework/rate-homework';
import NewFile from '../popup-content/new-file/new-file';
import NewTaskFile from '../popup-content/new-file/new-task-file';
import LeaveGroup from '../popup-content/leave-group';
import NewLesson from '../popup-content/new-lesson';
import DeleteLesson from '../popup-content/delete-lesson/delete-lesson';
import GroupPicture from '../popup-content/group-picture';
import DeleteMember from '../popup-content/delete-member/delete-member';
import MemberInfo from '../popup-content/member-info/member-info';


class PopUp extends Component {
    

    render() {

        const contentJson = {
            addJoin: <AddJoin handlePopUpContent={this.context.handlePopUpContent} changePopUpState={this.context.changePopUpState}/>,
            addGroupPopupContent: <AddGroupPopupContent changePopUpState={this.context.changePopUpState} />,
            joinGroupPopupContent: <JoinGroupPopupContent changePopUpState={this.context.changePopUpState} updateErrorMessage={this.context.updateErrorMessage} handlePopUpContent={this.context.handlePopUpContent}/>,
            userPicture: <UserPicture changePopUpState={this.context.changePopUpState} />,
            newTask: <NewTask currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            editTask: <EditTask currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            rateHomework: <RateHomework currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            newFile: <NewFile currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            leaveGroup: <LeaveGroup currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            newTaskFile: <NewTaskFile currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            newLesson: <NewLesson currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            deleteLesson: <DeleteLesson currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            groupPicture: <GroupPicture currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            deleteMember: <DeleteMember currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            memberInfo: <MemberInfo handlePopUpContent={this.context.handlePopUpContent} currentGroupLink={this.context.currentGroupLink} changePopUpState={this.context.changePopUpState} />,
            error: <Error errorMessage={this.context.errorMessage} changePopUpState={this.context.changePopUpState} />
        };

        // const popupSize = {
        //     width: small ? '27%' : '45%'
        // };

        return (
            <PopUpContext.Consumer>
                {
                    (contextProps) => {
                        const { changePopUpState, popUpContent } = contextProps;
                        let smallPopUp;
                        if(this.context.currentGroupLink !== null){
                            smallPopUp = this.context.currentGroupLink.smallPopUp;
                        } else{
                            smallPopUp = false;
                        }

                        return (
                            <div className={ smallPopUp ? "popup small" : "popup"}>
                                <img className="close" onClick={changePopUpState} src="/assets/svg/close.svg" alt="Close"></img>
                                {popUpContent ? contentJson[popUpContent] : null}
                                {/* {
                                React.Children.map(this.props.children, (child, idx) => {
                                    return child;
                                })
                                } */}
                            </div>
                        );
                    }
                }
            </PopUpContext.Consumer>
        );
    }
}

PopUp.contextType = PopUpContext;

export default PopUp;