import React, { Component } from "react";
import GroupService from '../../../services/group-service';

export default class LeaveGroup extends Component {

    state = {
        GroupService: new GroupService(),
    }

    leaveGroupAction = () => {
        this.state.GroupService.leaveGroup(this.props.currentGroupLink.GroupLink).then(
            res => {
                console.log(res);
                window.location = "/";
                // if (res["response"]["code"] === "100") {
                //     window.location = "/";
                // } else {
                //     alert(res["response"]["message"]);
                // }
            }
        );
    }

    render() {
        return (
            <div className="confirm">
                <div className="info">Are you sure you want to leave the group?</div>
                <div className="confirm-action">
                    <div className="yes" onClick={this.leaveGroupAction}>Yes</div>
                    <div className="no" onClick={this.props.changePopUpState}>No</div>
                </div>
            </div>
        );
    }
}
