import React, { Component } from 'react';
import '../auth/auth.css';

// import { withRouter } from 'react-router-dom';
import queryString from 'query-string';

import AuthService from '../../../../services/auth-service';

class Password extends Component {

    state = {
        AuthService: new AuthService(),
        showingButton: false,
        userPass: '',
        message: null,
        sMessage: null,
        userToken: null,
    };

    componentDidMount(){
        const parsed = queryString.parse(window.location.search);
        console.log(parsed);

        if(parsed.t !== undefined){
            this.setState({userToken: parsed.t})
        } else{
            window.location = "/";
        }
    }

    onPasswordFill = (e) => {
        this.setState({
            userPass: e.target.value
        });
    }

    onPasswordCheck = (e) => {
        if (e.target.value === this.state.userPass) {
            this.setState({
                showingButton: true
            });
        }
    }

    onNewPassFormSubmit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }
        this.state.AuthService.sendFormRequest(formData, '/auth/password').then(
            res => {
                console.log(res["response"]);
                if (res["response"]["code"] !== undefined) {
                    this.setState({
                        sMessage: res["response"]["message"]
                    });

                    function redirectToLogin() {
                        window.location = "/signin";
                      }
                      
                      setTimeout(redirectToLogin, 2000);
                } else{
                    this.setState({
                        message: res["response"]["message"]
                    });
                }
            }
        );
    }

    render() {

        const { showingButton, message, sMessage, userToken } = this.state;

        const buttonStyle = {
            display: showingButton ? 'block' : 'none'
        }

        const messageStyle = {
            display: message ? 'flex' : 'none'
        }

        const sMessageStyle = { //success message
            display: sMessage ? 'flex' : 'none'
        }

        return (
            <div className="auth">
                <div className="header">
                    <img className="logo" src="/assets/svg/logo.svg" alt="JetDesk" />
                    <a href="https://jetdesk.app/">
                        <span>JetDesk</span>
                    </a>
                </div>
                <div style={messageStyle} className="message">{message}</div>
                <div style={sMessageStyle} className="message s">{sMessage}</div>
                <div className="page-content">
                    <form id="restore" onSubmit={this.onNewPassFormSubmit}>
                        <h1>New Password</h1>
                        <div className="input_block_section">
                            <span>Pick a password</span>
                            <input type="password" onChange={this.onPasswordFill} name="password" id="passR" placeholder="Enter your password here" required />
                        </div>
                        <div className="input_block_section">
                            <span>Repeat password</span>
                            <input type="password" onChange={this.onPasswordCheck} name="password2" id="confirmpassR" placeholder="Repeat your password here" required />
                        </div>
                        <input type="hidden" name="userToken" defaultValue={userToken} />
                        <div className="actions absolute-on-mobile">
                        <button type="submit" style={buttonStyle}>Update</button>
                            {/* <a href="/signin">Go back</a> */}
                        </div>
                    </form>
                    <img className="illustration" src="/assets/illustration/enter.svg" alt="Door" />
                </div>
            </div>
        );
    }
}

export default Password; 