import React, { Component } from 'react';
import './new-popup.css';

import AddJoin from '../popup-content/add-join';
import AddGroupPopupContent from '../popup-content/add-join-next/add-group-popup-content';
import JoinGroupPopupContent from '../popup-content/add-join-next/join-group-popup-content';
import UserPicture from '../popup-content/user-picture';
import NewTask from '../popup-content/new-task/new-task';
import Error from '../popup-content/error';
import EditTask from '../popup-content/edit-task/edit-task';
import RateHomework from '../popup-content/homework/rate-homework';
import NewFile from '../popup-content/new-file/new-file';


class NewPopUp extends Component {

    state = {
        showingPopUp: false,
        popUpContent: null,
        errorMessage: null,
        currentGroupLink: null
      };


    changePopUpState = () => {
        this.setState({
          showingPopUp: !this.state.showingPopUp
        });
      }

      updateErrorMessage = (msg) => {
        this.setState({
          errorMessage: msg
        });
      }
    
      handlePopUpContent = (name, currentGroupLink) => {
        console.log("handlePopUpContent: " + name);
    
        if (currentGroupLink === undefined) {
          currentGroupLink = null;
        }
        console.log("currentGroupLink: " + currentGroupLink);
    
        const { showingPopUp, popUpContent } = this.state;
        if (popUpContent === name) {
          if (!showingPopUp) {
            this.setState({ showingPopUp: true, popUpContent: name, currentGroupLink: currentGroupLink });
          } else {
            this.setState({ showingPopUp: false, popUpContent: null, currentGroupLink: null });
          }
        } else {
          this.setState({ showingPopUp: true, popUpContent: name, currentGroupLink: currentGroupLink });
        }
      };


    render() {

        const { changePopUpState, updateErrorMessage, handlePopUpContent } = this;
        const { currentGroupLink, popUpContent } = this.state;

        const contentJson = {
            addJoin: <AddJoin handlePopUpContent={handlePopUpContent} />,
            addGroupPopupContent: <AddGroupPopupContent changePopUpState={changePopUpState} />,
            joinGroupPopupContent: <JoinGroupPopupContent changePopUpState={changePopUpState} updateErrorMessage={updateErrorMessage} handlePopUpContent={handlePopUpContent} />,
            userPicture: <UserPicture />,
            newTask: <NewTask currentGroupLink={currentGroupLink} changePopUpState={changePopUpState} />,
            editTask: <EditTask currentGroupLink={currentGroupLink} changePopUpState={changePopUpState} />,
            rateHomework: <RateHomework currentGroupLink={currentGroupLink} changePopUpState={changePopUpState} />,
            newFile: <NewFile currentGroupLink={currentGroupLink} changePopUpState={changePopUpState} />,
            error: <Error errorMessage={this.context.errorMessage} changePopUpState={changePopUpState} />
        };

        // const popupSize = {
        //     width: small ? '27%' : '45%'
        // };

        return (
            <div className="new-popup">
                <img className="close" onClick={changePopUpState} src="/assets/svg/close.svg" alt="Close"></img>
                {popUpContent ? contentJson[popUpContent] : null}
                {/* {
                                React.Children.map(this.props.children, (child, idx) => {
                                    return child;
                                })
                                } */}
            </div>
        );
    }
}

export default NewPopUp;