import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './../all-tasks/all-tasks-page.css';
import './admin.css';
import TopMenu from '../../top-menu';

import GroupService from '../../../services/group-service';
import PopUpContext from '../../context/popup-context';


// import { useParams } from "react-router-dom";


export default class ManageLessonsPage extends Component {

    state = {
        GroupService: new GroupService(),
        UserRole: null,
        GroupInfo: null,
        GroupLink: null,
        LessonsList: null,
    }

    componentDidMount() {
        const groupLink = this.props.match.params.link;
        this.state.GroupService.getGroupData(groupLink).then(
            res => {
                console.log(res);
                if (res["response"] !== false) {
                    this.state.GroupService.getRole(groupLink).then(
                        roleRes => {
                            // console.log(roleRes);
                            if (roleRes["response"] === "admin" || roleRes["response"] === "teacher") {
                                this.state.GroupService.getLessons(groupLink).then(
                                    lessonsRes => {
                                        console.log(lessonsRes);
                                        this.setState({
                                            UserRole: roleRes,
                                            GroupInfo: res["response"][0],
                                            LessonsList: lessonsRes["response"],
                                            GroupLink: res["response"][0]["link"],
                                        });
                                    });
                            } else {
                                window.location = "/";
                            }
                        });
                } else {
                    window.location = "/";
                }
            });

    }


// deleteLesson = (lessonId) => {
//     this.state.GroupService.deleteLesson(this.state.GroupInfo.link, lessonId).then(
//         res => {
//             console.log(res);
//             window.location.reload();
//         }
//     );
// }

    // componentDidUpdate(prevProps, prevState) {
    //     if (this.state.UserRole === "admin") {
    //         if (prevState.AllHomeWorks === this.state.AllHomeWorks) {
    //             this.state.TaskService.getAllHomework(this.state.TaskInfo.task_link, this.state.TaskInfo.group_link).then(
    //                 res => {
    //                     console.log(res);
    //                     this.setState({
    //                         AllHomeWorks: res["response"]
    //                     });
    //                 }
    //             );
    //         }
    //     }
    // }


    render() {

        const { GroupInfo, UserRole, LessonsList, GroupLink } = this.state;
        const { handlePopUpContent, reformatDate } = this.context;

        if (GroupInfo !== null) {


            const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']

            const AllLessonsBlock = () => LessonsList.map((event) =>
                <div className="tasks-row blue lessons-row" key={event.id}>
                    <div className="td name">{event.title}</div>
                    <div className="td delete-action-mobile">
                        <img onClick={() => handlePopUpContent("deleteLesson", {smallPopUp: true, link: GroupInfo.link, eventId: event.id})} className="delete_icon" src="/assets/svg/delete.svg" alt="Delete" />
                    </div>
                    {/* <div className="table_space" style={adminElements}></div> */}
                    {event.recurring === 0 ? <div className="td pc-line">{reformatDate(event.eventDate.substr(0, 10))}</div> : <div className="td pc-line">{days[event.days_of_week]}</div>}
                    <div className="td blue-text-mobile">{event.start_time.substr(0, 5)} {days[event.days_of_week]}</div>
                    <div className="td blue-text">{event.start_time.substr(0, 5)}</div>
                    {event.recurring === 0 ? <React.Fragment> <div className="td recurring-status pc">No</div> <div className="td recurring-status mobile">{reformatDate(event.eventDate.substr(0, 10))} </div> </React.Fragment>: <div className="td recurring-status">Weekly</div>}
                    {/* <div className="table_space"></div> */}
                    <div className="td delete-action-pc">
                        <img onClick={() => handlePopUpContent("deleteLesson", {smallPopUp: true, link: GroupInfo.link, eventId: event.id})} className="delete_icon" src="/assets/svg/delete.svg" alt="Delete" />
                    </div>
                    <div className="td short_description_mobile">{event.description.substr(0, 100)}</div>

                    {/* {task.homework ? TaskStatusOptions(task.homework.status) : <div style={userElements} className="td status red"> Not sent </div> } 
                {task.homework ? <div style={userElements} className="td mark">{task.homework.teacher_mark}</div> : <div style={userElements} className="td mark"> — </div> } 
                <div className="table_space" style={adminElements}></div>
                {task.homework ? TaskStatusOptionsMobile(task.homework.status) : <div className="td published-m">{reformatDate(task.created_date.substr(0, 10))}</div> } */}
                </div>
            )

            return (
                <React.Fragment>
                    {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} /> }

                    <div className="goback_button" onClick={() => window.history.back()}>
                        <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>

                    <div className="alltasks-page action-space admin-lessons">
                        <div className="page_heading">Lessons</div>
                        <div className="tasks-column-names lessons-row">
                            <span>Name</span>
                            <span>Date/Day</span>
                            <span>Time</span>
                            <span>Recurring</span>
                            {/* <div className="table_space"></div>
                            <span>Action</span> */}
                        </div>
                        <div className="tasks-column-rows">
                            {LessonsList ? <AllLessonsBlock /> : <p className="info_message">No lessons (events) yet...</p>}
                        </div>
                    </div>

                    <div className="button_new" onClick={() => handlePopUpContent("newLesson", GroupInfo.link)}>
                        <img src="/assets/svg/blue-plus.svg" alt="New" />
                    </div>
                </React.Fragment>
            );
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

ManageLessonsPage.contextType = PopUpContext;