import React, { Component } from 'react';

import Header from '../../../header';
import LeftMenu from '../../../left-menu';
import PopUp from '../../../popup';

import NewPopUp from '../../../popup/new-popup';

import PopUpContext from '../../../context/popup-context';

//UserPage
export default class DefaultLayout extends Component {


    state = {
        menuOpened: false,
    }

    handleTouchMove(e) {
        e.preventDefault();
      }

    openHideMenu = () => {
        console.log(this.state.menuOpened);
        if(this.state.menuOpened){
            console.log("unset")
            // document.body.style.overflow = 'unset';
            document.getElementById("page").removeEventListener('touchmove', this.handleTouchMove);
        } else{
            console.log("hidden")
            // document.body.style.overflow = 'hidden';
            // document.getElementById("page").addEventListener('touchmove', function(e){ e.preventDefault(); });
            document.getElementById("page").addEventListener('touchmove', this.handleTouchMove, false);
        }
        this.setState({
            menuOpened: !this.state.menuOpened
        });
        // document.body.style.overflow = 'unset';
    }

    onlyHideMenu = () => {
        this.setState({
            menuOpened: false
        });
        document.getElementById("page").removeEventListener('touchmove', this.handleTouchMove);
    }


    render() {

        return (
            <div className="app">
                <div className="layout">
                    <React.Fragment>
                        <LeftMenu menuOpened={this.state.menuOpened} openHideMenu={this.openHideMenu} onlyHideMenu={this.onlyHideMenu} handlePopUpContent={this.context.handlePopUpContent} serverUrl={this.context.serverUrl} />
                        <div className="page" id="page">
                            <Header menuOpened={this.state.menuOpened} openHideMenu={this.openHideMenu} serverUrl={this.context.serverUrl} currentGroupLink={this.context.currentGroupLink} />
                            <div className="page_content">
                                {
                                    React.Children.map(this.props.children, (child, idx) => {
                                        return child;
                                    })
                                }
                            </div>
                        </div>
                        {this.context.showingPopUp ? <div onClick={this.context.changePopUpState} className="popup-overlay" id="popupOverlay"></div> : null}
                        {this.context.showingPopUp ? <PopUp /> : null}
                        {/* <NewPopUp /> */}
                    </React.Fragment>
                </div>
            </div>
        );
    }
}


DefaultLayout.contextType = PopUpContext;