import React, { Component } from "react";
import GroupService from '../../../services/group-service';

export default class DeleteLesson extends Component {

    state = {
        GroupService: new GroupService(),
    }

    deleteLessonAction = (lessonId) => {
        this.state.GroupService.deleteLesson(this.props.currentGroupLink.link, lessonId).then(
            res => {
                console.log(res);
                window.location.reload();
            }
        );
    }

    render() {
        const eventId = this.props.currentGroupLink.eventId;

        return (
            <div className="confirm">
                <div className="info">Are you sure you want to delete this lesson?</div>
                <div className="confirm-action">
                    <div className="yes" onClick={() => this.deleteLessonAction(eventId)}>Yes</div>
                    <div className="no" onClick={this.props.changePopUpState}>No</div>
                </div>
            </div>
        );
    }
}
