import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './../all-tasks/all-tasks-page.css';
import './admin.css';

import TopMenu from '../../top-menu';
import GroupService from '../../../services/group-service';
import PopUpContext from '../../context/popup-context';


// import { useParams } from "react-router-dom";


export default class ManageMembersPage extends Component {

    state = {
        GroupService: new GroupService(),
        UserRole: null,
        GroupInfo: null,
        GroupLink: null,
        MembersList: null,
        TotalMembers: null,
        CurrentRoleMenu: null,
    }

    componentDidMount() {
        const groupLink = this.props.match.params.link;
        this.state.GroupService.getGroupData(groupLink).then(
            res => {
                console.log(res);
                if (res["response"] !== false) {
                    this.state.GroupService.getRole(groupLink).then(
                        roleRes => {
                            // console.log(roleRes);
                            if (roleRes["response"] === "admin" || roleRes["response"] === "teacher") {
                                this.state.GroupService.getGroupMembersAdmin(groupLink).then(
                                    membersRes => {
                                        console.log(membersRes);
                                        this.setState({
                                            UserRole: roleRes["response"],
                                            GroupInfo: res["response"][0],
                                            MembersList: membersRes["response"]["allUsers"],
                                            TotalMembers: membersRes["response"]["totalMembers"],
                                            GroupLink: res["response"][0]["link"],
                                        });
                                    });
                            } else {
                                window.location = "/";
                            }
                        });
                } else {
                    window.location = "/";
                }
            });

    }

    showRoleMenu = (num) => {
        document.getElementById("roleMenu" + num).style.display = "block";

        function checkForClick(event) {
            let isClickInside = document.getElementById("roleMenu" + num).contains(event.target);

            if (!isClickInside) {
                document.getElementById("roleMenu" + num).style.display = "none";
                document.removeEventListener("click", checkForClick, false);
            }
        }

        document.addEventListener('click', checkForClick);
    }

    setTeacher = (userId) => {
        this.state.GroupService.setTeacher(this.state.GroupInfo.link, userId).then(
            res => {
                console.log(res);
            if(res["response"]["code"] !== "100"){
                    alert("To many teachers for this group...");
                } else{
                    window.location.reload(); 
                }
            });
    }

    setTeacherToStudent = (userId) => {
        this.state.GroupService.setTeacherToStudent(this.state.GroupInfo.link, userId).then(
            res => {
                console.log(res);
                window.location.reload();
            });
    }



    render() {

        const { GroupInfo, UserRole, MembersList, TotalMembers, GroupLink } = this.state;
        const { handlePopUpContent, reformatDate, serverUrl } = this.context;

        if (GroupInfo !== null) {

            const AllMembersBlock = () => MembersList.map((member, key) =>
                <div className="tasks-row members blue members-row" key={key}>
                    <div className="td number">{key + 1}</div>
                    <div className="td user-picture">
                        <img src={serverUrl + member.userData.picture} alt={member.userData.name} />
                    </div>
                    <div className="td user-line">
                        {member.userData.name} {member.role === "teacher" ? <img className="teacher-icon" src="/assets/svg/hat.svg" alt="Teacher" /> : null}
                    </div>
                    <div className="td role pc-only">
                        {member.role} {member.role !== "admin" ? <img onClick={() => this.showRoleMenu(key)} className="down-icon" src="/assets/svg/down.svg" alt="Select" /> : null}

                        <div className="role-menu" id={"roleMenu" + key}>
                            {member.role === "teacher" ? <React.Fragment><div className="line blue"><img src="/assets/svg/check.svg" alt="Check" /> Teacher</div><div className="line" onClick={() => this.setTeacherToStudent(member.id)}>Student</div></React.Fragment> : <React.Fragment><div className="line blue"><img src="/assets/svg/check.svg" alt="Check" /> Student</div><div className="line" onClick={() => this.setTeacher(member.id)}>Teacher</div></React.Fragment>}
                        </div>
                    </div>

                    <div className="td rating pc-only">
                        {member.totalUserMark}
                    </div>
                    <div className="td delete pc-only">
                        {/* <img src="/assets/svg/trash.svg" alt="Delete" /> */}
                        {member.role !== "admin" ? <img className="delete_icon" src="/assets/svg/delete.svg" alt="Delete" onClick={() => handlePopUpContent("deleteMember", { smallPopUp: true, GroupLink: GroupInfo.link, UserId: member.id })} /> : null}
                    </div>
                    <div className="td rating more">
                    <img className="more_icon" src="/assets/svg/more.svg"  alt="More" onClick={() => handlePopUpContent("memberInfo", {GroupLink: GroupInfo.link, UserId: member.id}) } />
                    </div>
                </div>
            )


            return (
                <React.Fragment>
                    {UserRole === "admin" ? <TopMenu currentGroupLink={GroupLink} isAdmin /> : <TopMenu currentGroupLink={GroupLink} /> }

                    <div className="goback_button" onClick={() => window.history.back()}>
                        <img className="back_icon" src="/assets/svg/back.svg" alt="Back" />
                        Go back
                    </div>

                    <div className="alltasks-page action-space admin-lessons">
                        <div className="page_heading">Group Members</div>
                        {TotalMembers ? <div className="undone">Total members: {TotalMembers}</div> : null}
                        <div className="tasks-column-names members-row">
                            <span>Number</span>
                            <span></span>
                            <span>Full Name</span>
                            <span>Role</span>
                            <span>Rating</span>
                            <span></span>
                            {/* <div className="table_space"></div>
                            <span>Action</span> */}
                        </div>
                        <div className="tasks-column-rows">
                            {MembersList ? <AllMembersBlock /> : <p className="info_message">No members yet...</p>}
                        </div>
                    </div>
                </React.Fragment>
            );
        } else {
            return (
                <div className="grouppage">
                    {/* Loading... */}
                </div>
            );
        }
    }
}

ManageMembersPage.contextType = PopUpContext;