import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import TaskService from '../../../../services/task-service';

import PopUpContext from '../../../context/popup-context';

// import { useParams } from "react-router-dom";


export default class TasksBlock extends Component {

    state = {
        TaskService: new TaskService(),
        TasksList: null,
        GroupLink: null
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        const link = this.props.link;
        if (link !== this.state.GroupLink) {
            this.setState({
                GroupLink: link
            });
            this.state.TaskService.getTasks(this.props.link).then(
                res => {
                    console.log(res["response"]);
                    // if (res["response"] !== false) {
                    this.setState({
                        TasksList: res["response"]
                    });
                    // }
                });
        }
    }


    render() {

        const { TasksList } = this.state;
        const { reformatDate } = this.context;

        if (TasksList !== null) {
            const AllTasks = () => TasksList.map((task) =>
                <div className="task_slide" key={task.task_link}>
                    <div className="task_heading">
                        <div className={"task_circle small_circle " + task.color}></div>
                        {(task.name.length < 25 ? task.name : task.name.substr(0, 25) + "...")}
                    </div>
                    <div className="task_deadline">Untill {reformatDate(task.deadline.substr(0, 10))}</div>
                    <div className="task_short_description">
                        {(task.comment.length < 90 ? task.comment : task.comment.substr(0, 90) + "...")}
                    </div>
                    <Link to={"/task/" + task.task_link}><button className="main task_more">Open the task</button></Link>
                    <Link to={"/task/" + task.task_link}><button className="main task_more mobile">More</button></Link>
                </div>
            );

            if (TasksList !== false) {
                return (
                    <div className="tasks_slider">
                        <AllTasks />
                    </div>
                );
            } else {
                return (
                    <div className="widget_info">
                        <div className="info-message">
                            <img className="info-illustration" src="/assets/illustration/desk-gray.svg" alt="Desk" />
                            <span>The teacher hasn't posted any tasks yet. You can rest! </span>
                        </div>
                    </div>
                );
            }
        } else {
            return (
                <p>Loading...</p>
            );
        }
    }
}

TasksBlock.contextType = PopUpContext;