import React, { Component } from 'react';

import parse from 'html-react-parser';
// import { Link } from 'react-router-dom';

import GroupService from '../../../../services/group-service';

import PopUpContext from '../../../context/popup-context';

// import { useParams } from "react-router-dom";


export default class ChatBlock extends Component {

    state = {
        GroupService: new GroupService(),
        ChatMessages: null,
        GroupLink: null,
        CurrentChatPage: 1,
        TotalChatPages: null,
        refreshChat: false,
        UserInputText: "",
        // showingRatingReloadInfo: null //to reload the block
    }

    componentDidMount() {
        this.componentDidUpdate();
        // this.chatInterval = setInterval(() => this.getChatData(this.state.CurrentChatPage), 60000);
    }

    componentDidUpdate() {
        const refreshChat = this.props.refreshChat;
        const link = this.props.link;
        // if (link !== this.state.GroupLink || showingRating !== this.state.showingRatingReloadInfo) {
        if (link !== this.state.GroupLink || refreshChat !== this.state.refreshChat) {
            this.setState({
                GroupLink: link,
                refreshChat: refreshChat,
                UserInputText: "",
                // showingRatingReloadInfo: this.props.showingRating
            });

            this.getChatData(1);
        }
    }

    componentWillUnmount() {
        // Clear the interval right before component unmount
        // clearInterval(this.chatInterval);
    }

    getChatData = (page) => {
        console.log("getChatData page: " + page);

        this.state.GroupService.getGroupChat(this.props.link, page).then(
            res => {
                console.log(res);
                this.setState({
                    ChatMessages: res["response"]["data"],
                    CurrentChatPage: res["response"]["page"],
                    TotalChatPages: res["response"]["totalpages"],
                });

                if (res["response"]["data"] !== undefined) {
                    // clearInterval(this.chatInterval);
                    // this.chatInterval = setInterval(() => this.getChatData(this.state.CurrentChatPage), 60000);
                } else {
                    // clearInterval(this.chatInterval);
                }
            });
    }

    sendMessage = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        console.log(formData);

        this.state.GroupService.newMessage(formData).then(
            res => {
                console.log(res);
                this.setState({ UserInputText: "" });

                if(document.getElementById("userMessageInput1") !== null){
                    document.getElementById("userMessageInput1").value = ""; 
                }

                if(document.getElementById("userMessageInput2") !== null){
                    document.getElementById("userMessageInput2").value = ""; 
                }


                this.getChatData(1);
                // if (res["response"]["code"] === "100") {
                //     // window.location.reload();
                // } else {
                //     alert("Error! Check console");
                // }
            }
        );
    }

    workWithMessage = (msg) => {
        function urlify(text) {
            var urlRegex = /(https?:\/\/[^\s]+)/g;
            return text.replace(urlRegex, function (url) {
                let prettyURL = url.replace(/(^\w+:|^)\/\//, '');
                return `<a href="${url}" target="_blank">${prettyURL}</a>`;
            })
            // or alternatively
            // return text.replace(urlRegex, '<a href="$1">$1</a>')
        }

        function youtube_parser(url) {
            var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
            var match = url.match(regExp);
            return (match && match[7].length == 11) ? match[7] : false;
        }

        function checkImageURL(url) {
            return (url.match(/\.(jpeg|jpg|gif|png)$/) != null);
        }

        let html;

        if (youtube_parser(msg)) { //check if msg is a YouTube video
            html = `${msg} <br /><br /> <iframe key="${youtube_parser(msg)}" src="https://www.youtube.com/embed/${youtube_parser(msg)}" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`;
        } else {
            if (checkImageURL(msg)) { //check if msg is Img
                html = `${msg} <br /><br /> <img src="${msg}" alt="${msg}" />`
            } else {
                html = urlify(msg);
            }
        }

        html = parse(html);
        // console.log(html)
        return html;
    }

    loadMoreMessages = () => {
        this.getChatData(parseInt(this.state.CurrentChatPage) + 1);
    }

    handleInputChange = (e) => {
        // console.log(e.target.value);
        this.setState({ UserInputText: e.target.value });
    }


    render() {

        const { ChatMessages, GroupLink, CurrentChatPage, TotalChatPages, UserInputText } = this.state;
        const { serverUrl } = this.context;

        if (ChatMessages !== null) {

            const ChatMessagesBlock = () => ChatMessages.map((msgData, i, array) => {
                if (array[i + 1] !== undefined) {
                    if (array[i].user_id !== array[i + 1].user_id) {
                        // console.log(array[i].user_id + " is different from " + array[i - 1].user_id);
                        return (
                            <div className="chat-message" key={msgData.id}>
                                <img className="user-picture" src={serverUrl + msgData.userData.picture} alt="User Pic" />
                                <div className="message-wrapper">
                                    <span className="name">{msgData.userData.name}</span>
                                    <div className="message" key={"message" + msgData.id}>{this.workWithMessage(msgData.message)}</div>
                                </div>
                            </div>
                        );
                    } else {
                        return (
                            <div className="chat-message me" key={msgData.id}>
                                <div className="message-wrapper me">
                                    <div className="message" key={"message" + msgData.id}>{this.workWithMessage(msgData.message)}</div>
                                </div>
                            </div>
                        );
                    }
                } else{
                    return (
                        <div className="chat-message" key={msgData.id}>
                            <img className="user-picture" src={serverUrl + msgData.userData.picture} alt="User Pic" />
                            <div className="message-wrapper">
                                <span className="name">{msgData.userData.name}</span>
                                <div className="message" key={"message" + msgData.id}>{this.workWithMessage(msgData.message)}</div>
                            </div>
                        </div>
                    );
                }
            }
            );

            const LoadMoreMessagesButton = (props) => {
                return (
                    <span className="load-more" onClick={() => this.loadMoreMessages()}>Show older messages</span>
                )
            }


            // const MessageInput = (props) => {
            //     return (
            //         <div className="message-input">
            //             <div className="wrapper">
            //                 <form onSubmit={this.sendMessage}>
            //                     <input name="message" type="text" placeholder="Enter your message..." autoComplete="off" key="userMessageInput3" onChange={this.handleInputChange} value={UserInputText} />
            //                     <input name="link" type="hidden" value={GroupLink} />
            //                     <button type="submit" className="send"></button>
            //                     {/* <img src="/assets/svg/send-message.svg" className="send" alt="Send" /> */}
            //                 </form>
            //             </div>
            //         </div>
            //     );
            // }

            if (ChatMessages !== undefined) {
                return (
                    <div className="chat" key="chat">
                        <div className="chat-messages-list" key="chat-messages-list">
                            <ChatMessagesBlock key="ChatMessagesBlock" />
                            {parseInt(TotalChatPages) !== parseInt(CurrentChatPage) ? <LoadMoreMessagesButton /> : null}
                        </div>

                        <div className="message-input" key="message-input1">
                            <div className="wrapper">
                                <form onSubmit={this.sendMessage}>
                                    {/* <input name="message" type="text" placeholder="Enter your message..." autoComplete="off" key="userMessageInput1" onChange={this.handleInputChange} value={UserInputText} /> */}
                                    <input name="message" type="text" placeholder="Enter your message..." autoComplete="off" id="userMessageInput1" key="userMessageInput1" />
                                    <input name="link" type="hidden" value={GroupLink} />
                                    <button type="submit" className="send"></button>
                                </form>
                            </div>
                        </div>
                        {/* <MessageInput /> */}
                    </div>
                );
            } else {
                return (
                    <div className="widget_info chat">
                        {/* <span>Be the first one to leave a message... </span> */}
                        <div className="info-message">
                            <img className="info-illustration" src="/assets/illustration/plane.svg" alt="Plane" />
                            <span>Be the first one<br /> to leave a message!</span>
                        </div>

                        <div className="message-input" key="message-input2">
                            <div className="wrapper">
                                <form onSubmit={this.sendMessage}>
                                    {/* <input name="message" type="text" placeholder="Enter your message..." autoComplete="off" key="userMessageInput2" onChange={this.handleInputChange} value={UserInputText} /> */}
                                    <input name="message" type="text" placeholder="Enter your message..." autoComplete="off" id="userMessageInput2" key="userMessageInput2" />
                                    <input name="link" type="hidden" value={GroupLink} />
                                    <button type="submit" className="send"></button>
                                </form>
                            </div>
                        </div>
                        {/* <MessageInput /> */}
                    </div>
                );
            }
        } else {
            return (
                <p>Loading...</p>
            );
        }
    }
}

ChatBlock.contextType = PopUpContext;