import React, { Component } from 'react';
import '../auth/auth.css';

import AuthService from '../../../../services/auth-service';

class SignIn extends Component {

    state = {
        AuthService: new AuthService(),
        userPass: '',
        showingButton: false,
        message: null
    };

    onPasswordFill = (e) => {
        this.setState({
            userPass: e.target.value
        });
    }

    onPasswordCheck = (e) => {
        if (e.target.value === this.state.userPass) {
            this.setState({
                showingButton: true
            });
        }
    }

    onRegFormSubmit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.
        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.AuthService.sendFormRequest(formData, '/auth/register').then(
            res => {
                this.setState({
                    message: res["message"]
                });
            }
        );
    }

    onLoginFormSubmit = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }
        this.state.AuthService.sendFormRequest(formData, '/auth/login').then(
            res => {
                if (res["response"]["message"] !== undefined) {
                    this.setState({
                        message: res["response"]["message"]
                    });
                } else {
                    localStorage.setItem('token', res["response"]["token"]);
                    this.state.AuthService.getUserData(res["response"]["token"]).then(
                        userData => {
                            localStorage.setItem('user', JSON.stringify(userData));
                            this.props.logIn();
                        });
                }
            }
        );
    }

    render() {
        const { showingButton, message } = this.state;

        const buttonStyle = {
            display: showingButton ? 'block' : 'none'
        }

        const messageStyle = {
            display: message ? 'flex' : 'none'
        }

        return (
            <div className="auth">
                <div className="header">
                    <img className="logo" src="/assets/svg/logo.svg" alt="JetDesk" />
                    <a href="https://jetdesk.app/">
                        <span>JetDesk</span>
                    </a>
                </div>
                <div style={messageStyle} className="message">{message}</div>
                <div className="page-content">
                    <form id="login" onSubmit={this.onLoginFormSubmit}>
                        <h1>Sign in</h1>
                        <div className="input_block_section">
                            <span>Email</span>
                            <input type="email" name="email" id="emailL" placeholder="Enter your email here" required />
                        </div>
                        <div className="input_block_section">
                            <span>Password</span>
                            <input type="password" onChange={this.onPasswordFill} name="password" id="passL" placeholder="Enter your password here" required />
                        </div>
                        <a href="/restore" className="link">Forgot your password?</a>
                        <div className="actions absolute-on-mobile">
                            <button type="submit">Sign in</button>
                            <a href="/signup">I don't have an account</a>
                        </div>
                    </form>
                    <img className="illustration" src="/assets/illustration/enter.svg" alt="Door" />
                </div>
            </div> 
        );
    }
}

export default SignIn; 