import React, { Component } from "react";

import GroupService from '../../../services/group-service';

export default class JoinGroupPopupContent extends Component {

    state = {
        GroupService: new GroupService(),
    }

    onGroupJoin = (e) => {
        e.preventDefault(); // avoid to execute the actual submit of the form.

        // console.log(e.target);
        let formData = [];
        for (let i = 0; i < e.target.length - 1; i++) {
            formData[e.target[i].name] = e.target[i].value;
        }

        this.state.GroupService.joinGroup(formData).then(
            res => {
                console.log(res);
                if (res["response"]["code"] === "100") {
                    window.location = "/class/" + res["response"]["link"];
                } else {
                    // alert(res["response"]["message"]);
                    this.props.updateErrorMessage(res["response"]["message"]);
                    this.props.handlePopUpContent("error");
                }
            }
        );
    }

    render() {
        return (
            <div className="popup_form">
                <h4>Join a class</h4>
                <form onSubmit={this.onGroupJoin}>
                    <div className="form_elements">
                        <div className="form_input_section">
                            <input type="text" name="link" placeholder="Enter group ID" autoComplete="off" required />
                            <span className="label">Group ID</span>
                        </div>
                        <div className="form_input_section">
                            <input type="text" name="code" placeholder="Enter invitation pass" autoComplete="off" />
                            <span className="label">Invitation pass (if required)</span>
                        </div>
                        {/* <div className="form_input_section">
                        <input type="text" name="schoolName" placeholder="(Could be your or a personal brand)" required/>
                        <span className="label">School name</span>
                    </div> */}
                    </div>
                    <div className="form_action">
                        <div className="cancel" onClick={this.props.changePopUpState}>Cancel</div>
                        <button className="main" type="submit">Join</button>
                    </div>
                </form>
            </div>
        );
    }
}