import React, { Component } from 'react';
import './left-menu.css';

// import { BrowserRouter, NavLink, Nav, Link } from 'react-router-dom';
import { NavLink, Link } from 'react-router-dom';

import LazyImage from "../../components/lazy-load/lazy-load-image";

import GroupService from '../../services/group-service';

class LeftMenu extends Component {

    state = {
        GroupService: new GroupService(),
        UserGroups: null,
        // menuOpened: false,
    }

    componentDidMount() {
        // console.log("I did mount")
        this.state.GroupService.getFollowing().then(
            res => {
                console.log(res);
                this.setState(
                    {
                        UserGroups: res["response"]
                    });
            });
    }


    // onlyHideMenu = () => {
    //     this.setState({
    //         menuOpened: false
    //     });
    // }

    render() {
        const { handlePopUpContent, menuOpened, openHideMenu, onlyHideMenu, serverUrl } = this.props;

        const { UserGroups } = this.state;

        //https://jsfiddle.net/0ky72bvh/

        const generateGroupShortName = (data) => {
            let name;
            if (data.split(' ').length >= 2) {
                let twoWords = data.split(' ').slice(0, 2).join(' ')
                let matches = twoWords.match(/\b(\w)/g);
                let acronym = matches.join('');
                name = acronym.substring(0, 2);
            } else {
                name = data.substring(0, 2);
            }
            return name;
        }

      

        const ShowGroups = () => UserGroups.map((group) =>
            <NavLink activeclassname='active' to={"/class/" + group.link} onClick={onlyHideMenu} className="leftmenu_link" key={group.link}>
                <div className="menu_circle">
                    {group.picture !== "data/uploads/images/default/group.png" ? <img src={serverUrl + group.picture} title={group.name} alt={group.name} /> : generateGroupShortName(group.name)}
                    {/* {group.picture !== "data/uploads/images/default/group.png" ?  <LazyImage key={"img" + group.name} src={serverUrl + group.picture} alt={group.name} /> : generateGroupShortName(group.name)} */}
                </div>
                <div className="full_class_info">
                    <p>{group.name}</p>
                    <span>{group.school_name}</span>
                </div>
            </NavLink>
        );

        return (
            <div className={menuOpened ? 'leftmenu opened' : 'leftmenu'}>
                <img className="menu" onClick={openHideMenu} src="/assets/svg/pc-menu.svg" alt="Menu"></img>
                <div className="leftmenu_top_line">
                    <div className="menu_logo_block">
                        <Link to="/">
                            <img className="menu_logo" src="/assets/svg/logo-white.svg" alt="Logo"></img>
                        </Link>
                        <div className="menu_logo_text">JetDesk</div>
                    </div>
                    <img className="menu_close" onClick={openHideMenu} src="/assets/svg/menu-close-new.svg" alt="Close Menu"></img>
                </div>
                <div className="groups">
                    {/* <BrowserRouter>
                <Nav> */}
                    {(UserGroups ? <ShowGroups /> : null)}
                    {/* </Nav>
                </BrowserRouter> */}
                </div>
                <div className="leftmenu_bottom" onClick={() => handlePopUpContent("addJoin")}>
                    <img className="plus" src="/assets/svg/plus.svg" alt="Plus"></img>
                </div>
                <div className="leftmenu_bottom_opened">
                    <div className="leftmenu_bottom_opened_text">
                        <p>Join or create a Class</p>
                    </div>
                    {/* <div className="leftmenu_plus_square" onClick={() => handlePopUpContent("addJoin")}> */}
                    <div className="leftmenu_plus_square" onClick={() => handlePopUpContent("addJoin", { smallPopUp: true })}>
                        <img className="plus" src="/assets/svg/plus.svg" alt="Plus"></img>
                    </div>
                </div>
            </div>
        );
    }
}

export default LeftMenu;