import React, { Component } from "react";
import GroupService from '../../../services/group-service';

export default class DeleteMember extends Component {

    state = {
        GroupService: new GroupService(),
    }

    deleteMemberAction = () => {
        this.state.GroupService.deleteGroupMember(this.props.currentGroupLink.GroupLink, this.props.currentGroupLink.UserId).then(
            res => {
                console.log(res);
                window.location.reload();
                // window.location = "/";
            }
        );
    }

    render() {
        return (
            <div className="confirm">
                <div className="info">Are you sure you want to remove this group member?</div>
                <div className="confirm-action">
                    <div className="yes" onClick={this.deleteMemberAction}>Yes</div>
                    <div className="no" onClick={this.props.changePopUpState}>No</div>
                </div>
            </div>
        );
    }
}
